.other-product {
	margin-bottom: 40px;
	h2 {
		padding-top: 40px;
		font-size: r(35px);
		font-family: "Muli";
		color: rgb(237, 28, 36);
		line-height: r(42px);
		text-align: center;
		font-weight: 900; }
	@media screen and(max-width:1600px) {
		h2 {
			font-size: r(30px);
			line-height: r(36px); } }
	@media screen and(max-width:1200px) {
		margin-bottom: 30px;
		h2 {
			padding-top: 30px;
			font-size: r(24px);
			line-height: r(30px); } }
	@media screen and(max-width:768px) {
		margin-bottom: 20px;
		h2 {
			padding-top: 20px;
			font-size: r(22px);
			line-height: r(26px); } }
	.swiper-other-product-inclue {
		width: 100%;
		height: 100%;
		position: relative;
		&:hover {
			.button-prev-product,.button-next-product {
				opacity: 1; }
			.swiper-button-disabled {
				opacity: .3; } }
		.button-prev-product,.button-next-product {
			background: #ed1c24;
			opacity: 1;
			outline: none;
			width: r(35px);
			height: r(35px);
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			z-index: 99;
			top: 50%;
			transform: translateY(-50%);
			opacity: 0;
			-webkit-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
			-moz-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
			-o-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
			transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
			-webkit-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
			-moz-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
			-o-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
			transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
			i {
				color: #fff;
				font-size: 1.5rem; }
			&:hover {
				border-radius: 50%; } }
		.swiper-button-disabled {
			opacity: 0;
			cursor: auto;
			pointer-events: auto; }
		.button-prev-product {
			left: 0; }
		.button-next-product {
			right: 0; }
		.swiper-wrapper {
			padding: r(25px) r(20px) 0 r(20px);
			@media screen and(max-width:1600px) {
				margin: 0 .15rem;
				padding: r(25px) r(5px); }
			@media screen and(max-width:1200px) {
					padding: r(25px) r(5px);
					margin: 0 .15rem; }
			.swiper-slide {
				padding-bottom: .25rem;
				display: -webkit-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				-webkit-box-pack: center;
				-ms-flex-pack: center;
				-webkit-justify-content: center;
				justify-content: center;
				-webkit-box-align: center;
				-ms-flex-align: center;
				-webkit-align-items: center;
				align-items: center;
				&:hover {
					.figure {
						-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
						-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
						box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
						.figure-image {
							.add-cart {
								display: flex;
								opacity: 1; } } } }
				.figure {
					position: relative;
					margin-bottom: 0;
					width: 98%;
					&:hover {
						.figure-caption {
							background: #fafafa; } }
					.hot-item {
						position: absolute;
						top: .005rem;
						left: 0;
						background: #ed1c24;
						z-index: 11;
						h5 {
							margin: 0 0;
							padding: .1rem .75rem;
							font-size: r(16px);
							font-family: 'Muli';
							color: rgb(247, 247, 247);
							font-weight: bold; } }
					@include figure-caption-product;
					.figure-image {
						position: relative;
						height: 19.675rem;
						width: auto;
						img {
							width: 100%;
							height: 100%;
							object-fit: scale-down; }
						@media screen and(max-width:1919px) {
							.figure-image {
								height: r(270px); } }
						@media screen and(max-width:1200px) {
							.figure-image {
								padding: 0 1rem;
								height: 14.875rem; } }
						.add-cart {
							display: none;
							opacity: 0;
							position: relative;
							margin-top: -3rem;
							justify-content: center;
							align-items: center;
							ul {
								padding: .25rem .5rem;
								background: url('../images/options-1/bg-linear-gradient-gold-small2.png');
								background-repeat: no-repeat;
								background-size: cover;
								background-position: center;
								display: flex;
								justify-content: center;
								align-items: center;
								list-style-type: none;
								li {
									&:hover {
										a {
											&:after {
												opacity: 1; } }
										.label {
											opacity: 1; } }
									a {
										text-decoration: none;
										position: relative;
										&:after {
											top: -.6rem;
											left: 50%;
											border: solid transparent;
											content: " ";
											height: 0;
											opacity: 0;
											width: 0;
											position: absolute;
											pointer-events: none;
											border-color: rgba(237, 28, 36, 0);
											border-top-color: #ed1c24;
											border-width: 7px;
											margin-left: -7px; }
										i {
											color: #ed1c24;
											margin: 0 .75rem; } }
									.label {
										left: 50%;
										opacity: 0;
										transform: translateX(-50%);
										position: absolute;
										top: -2.5rem;
										padding: .25rem .5rem;
										background: #ed1c24;
										display: flex;
										justify-content: center;
										align-items: center;
										p {
											color: #fff;
											margin-bottom: 0;
											font-size: r(15px);
											font-family: 'Muli';
											color: rgb(255, 255, 255); } } } }
							@media screen and (max-width:521px) {
								ul {
									li {
										.label {
											p {
												font-size: r(13px);
												line-height: r(21px);
												white-space: nowrap; } } } } } } } } } } }
	.swiper-pagination-wrapper {
		display: none;
		justify-content: center;
		align-items: center;
		.swiper-pagination {
			.swiper-pagination-bullet {
				background: #EC1F26;
				opacity: 1; }
			.swiper-pagination-bullet-active {
				background: -moz-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: -webkit-gradient(linear, left bottom, right top, color-stop(0%, rgba(255,0,0,1)), color-stop(50%, rgba(255,255,0,1)), color-stop(100%, rgba(255,0,0,1)));
				background: -webkit-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: -o-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: -ms-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff0000', endColorstr='#FF0000',GradientType=1 ); }
			span {
				&:not(:last-child) {
					margin-right: .425rem; } } } } }
