.kingsport-footer-2v2 {
	background: #f5f5f5;
	padding: r(50px) 0;
	.footer-title {
		display: flex;
		flex-flow: column;
		justify-content: center;
		align-items: center;
		margin-bottom: r(30px);
		h4 {
			font-size: r(18px);
			letter-spacing: r(16px*.0625);
			line-height: r(24px);
			font-family: 'Muli';
			color: #000;
			font-weight: 400; }
		.form {
			.form-group {
				display: flex;
				justify-content: center;
				align-items: center;
				input {
					width: r(350px);
					height: r(40px);
					padding: 0 1rem;
					border: 1px solid #fff;
					background: #cacaca;
					color: #fff;
					&::placeholder {
						font-family: 'Roboto';
						font-size: r(16px);
						font-family: 'Muli';
						color: #666666; } }
				a {
					position: relative;
					overflow: hidden;
					width: r(120px);
					height: r(40px);
					background: #ed1c24;
					border: 1px solid #fff;
					display: flex;
					justify-content: center;
					align-items: center;
					text-decoration: none;
					font-size: r(18px);
					font-family: 'Muli';
					color: rgb(255, 255, 255);
					font-weight: 500;
					letter-spacing: r(18px*.0625);
					line-height: r(26px); } } }
		p {
			margin-top: .5rem;
			font-size: r(13px);
			font-family: 'Muli';
			color: #afafaf;
			letter-spacing: r(13px*.0625);
			line-height: r(21px); } }
	@media screen and(max-width:1600px) {
		.footer-title {
			h4 {
				font-size: r(16px); }
			.form {
				.form-group {
					input {
						&::placeholder {
							font-size: r(14px); } }
					a {
						font-size: r(16px); } } }
			p {
				font-size: r(13px); } } }
	@media screen and(max-width:768px) {
		.footer-title {
			h4 {
				text-align: center;
				padding: 0 2rem; } } }
	@media screen and(max-width:576px) {
		padding: 1.5rem 0 0 0;
		.space-bet {
			display: none; }
		.footer-title {
			margin-bottom: .25rem;
			h4 {
				padding: 0 1rem;
				font-size: r(16px);
				letter-spacing: r(16px*.0625);
				line-height: r(20px);
				font-weight: 500; }
			p {
				text-align: center; }
			.form {
				.form-group {
					input {
						background: #cacaca;
						width: 15rem;
						font-size: r(15px);
						&::placeholder {
							font-size: r(14px); } }
					a {
						font-size: r(15px); } } } } }
	@media screen and(max-width:400px) {
		.footer-title {
			.form {
				.form-group {
					input {
						width: 10rem;
						font-size: r(14px);
						&::placeholder {
							font-size: r(13px); } }
					a {
						font-size: r(14px); } } } } }
	.space-bet {
		h4 {
			font-size: r(16px);
			margin-bottom: 0;
			font-family: 'Muli';
			margin-bottom: 1.25rem;
			color: #333333;
			font-weight: bold;
			text-transform: uppercase;
			letter-spacing: r(16px*.0625);
			line-height: r(24px); } }
	.pad-0 {
		padding: 0 0;
		margin: 0 0; }
	.col-wrapper {
		h4 {
			font-size: r(16px);
			margin-bottom: 0;
			font-family: 'Muli';
			color: #333333;
			font-weight: bold;
			text-transform: uppercase;
			letter-spacing: r(16px*.0625);
			line-height: r(24px); }
		ul {
			list-style: none;
			padding-inline-start: 0;
			li {
				&:not(:last-child) {
					margin-bottom: -1rem; }
				&:before {
					content: '\2022';
					color: #ed1c24;
					padding-right: 0.35rem;
					position: relative;
					font-size: xx-large;
					top: 0.5rem; }
				a {
					text-decoration: none;
					font-size: r(16px);
					font-family: 'Muli';
					color: #666666;
					letter-spacing: r(14px*.0625);
					line-height: r(24px); } } } } }
