.kingsport-home-1v3 {
	background: url('../images/options-1/bg-linear-gradient-gold.png');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	-webkit-box-shadow: inset 0px 5px 0px 0px #684002;
	-moz-box-shadow: inset 0px 5px 0px 0px #684002;
	box-shadow: inset 0px 5px 0px 0px #684002;
	.home-wrapper {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		.navbar-mega {
			background: #fffcf7;
			width: r(285px);
			margin-top: -0.7rem;
			.navbar-list {
				margin-bottom: 0;
				list-style-type: none;
				padding: 0 0;
				position: relative;
				.nav-link {
					padding: .5rem 0 0.55rem 0;
					-webkit-box-shadow: 0px 1px 0px 0px rgba(250,242,225,1);
					-moz-box-shadow: 0px 1px 0px 0px rgba(250,242,225,1);
					box-shadow: 0px 1px 0px 0px rgba(250,242,225,1);
					@include nav-sub;
					&:hover {
						background: #ecf0f1;
						.material-line:before {
							width: 100%; }
						a {
							margin-left: 2.5rem; }

						.mega-menu {
							opacity: 1;
							background: #fffcf7;
							visibility: visible;
							ul {
								display: inline; } } }
					a {
						margin-left: 2.2rem;
						-webkit-transition: .3s all linear;
						-moz-transition: .3s all linear;
						-o-transition: .3s all linear;
						transition: .3s all linear; }
					.material-line {
						position: relative;
						display: block;
						width: 100%;
						&:before {
							content: '';
							display: block;
							height: 2px;
							background: rgba(#EE1C25,.7);
							width: 0;
							z-index: 100;
							position: absolute;
							bottom: r(-12px);
							left: 50%;
							transform: translateX(-50%);
							transition: .35s all ease-in-out; } }

					&:first-child {
						padding: 1.5rem 0 1rem 0;
						box-shadow: none;
						a {
							margin-left: .5rem;
							font-size: r(18px);
							font-weight: 700;
							font-family: 'Muli'; } }
					&:last-child {
						padding: .25rem .25rem 1.5rem .75rem;
						box-shadow: none;
						padding: .5rem 0; }
					a {
						font-family: 'Muli';
						display: block;
						font-size: r(16px);
						font-weight: 600;
						color: #333333;
						text-transform: uppercase;
						line-height: r(24px);
						text-decoration: none;
						.category {
							display: flex;
							justify-content: center;
							align-items: center;
							i {
								color: white;
								font-size: 1.5rem;
								display: flex;
								justify-content: flex-start;
								align-items: center;
								span {
									margin-left: .5rem;
									font-size: r(16px);
									font-family: 'Muli';
									line-height: r(24px);
									font-weight: 500;
									margin-bottom: 0; } } } } } } }

		.home-video-wrapper {
			border-top: r(22px) solid rgba(#000,0);
			border-left: r(22px) solid rgba(#000,0);
			width: calc(100% - 290px);
			z-index: 0;
			.box {
				&:first-child {
					border-right: r(20px) solid rgba(#000,0); }
				.video-content {
					position: relative;
					display: block;
					width: 100%;
					padding: 0;
					overflow: hidden;
					padding-top: 47.257143%;
					&:before {
						display: block;
						content: ""; }
					iframe {
						position: absolute;
						top: 0;
						bottom: 0;
						left: 0;
						width: 100%;
						height: 100%;
						border: 0; } }
				.figure {
					position: relative;
					overflow: hidden;
					&:before {
						position: absolute;
						top: 0;
						left: -75%;
						z-index: 2;
						display: block;
						content: '';
						width: 50%;
						height: 100%;
						background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
						background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
						-webkit-transform: skewX(-25deg);
						transform: skewX(-25deg); }
					&:hover {
						&:before {
							-webkit-animation: shine 1s;
							animation: shine 1s;
							@-webkit-keyframes shine {
								100% {
									left: 125%; } }
							@keyframes shine {
								100% {
									left: 125%; } } } }
					.figure-image {
						img {
							width: 100%; } } } } } }

	@media screen and(max-width:1600px) {
		.home-wrapper {
			.navbar-mega {
				width: r(238px);
				.navbar-list {
					.nav-link {
						padding: .3rem .25rem 0.3rem 0;
						a {
							margin-left: 1.25rem;
							font-size: r(14px);
							line-height: r(22px); }
						.material-line {
							&:before {
								bottom: r(-7px); } }
						&:hover {
							a {
								margin-left: 1.75rem; } }
						&:first-child {
							padding: 1.2rem 0 .5rem 0;
							box-shadow: none;
							a {
								margin-left: .5rem;

								.category {
									i {
										font-size: r(15px);
										span {
											font-size: r(15px);
											line-height: r(23px); } } } } }
						&:last-child {
							box-shadow: none; }
						.nav-sub {
							ul {} } } } }
			.home-video-wrapper {
				width: calc(100% - 200px);
				.box {
					.video-content {
						padding-bottom: 1%;
						min-height: auto;
						height: 0; }
					.figure {
						.figure-image {
							img {
								width: 100%; } } } } } } }
	@media screen and(max-width:1200px) {
		.home-wrapper {
			.navbar-mega {
				width: r(192px);
				.navbar-list {
					.nav-link {
						padding: .2rem 0 .2rem 0;
						a {
							font-size: r(11px);
							line-height: r(20px); }
						&:first-child {
							padding: 1.2rem 0 0.5rem 0;
							a {
								.category {
									i {
										font-size: r(15px);
										span {
											font-size: r(13px);
											line-height: r(21px); } } } } } } } }
			.home-video-wrapper {
				width: calc(100% - 190px); } } }
	@media screen and(max-width:992px) {
		display: none; } }
