.kingsport-bottom-banner {
	background: #de1f25;
	margin-bottom: 1rem;
	.banner-row-wrapper {
		padding: 1rem 0;
		display: flex;
		justify-content: center;
		align-items: center;
		.figure {
			margin: 0 0;
			display: flex;
			justify-content: center;
			align-items: center;
			.figure-image {
				width: r(80px);
				min-width: r(80px);
				height: r(80px);
				display: flex;
				justify-content: center;
				align-items: center;
				background: white;
				border-radius: 50%; }
			.figure-caption {
				margin-left: 1rem;
				p {
					font-size: r(16px);
					letter-spacing: r(16px*.0625);
					line-height: r(24px);
					text-transform: uppercase;
					font-family: "Roboto";
					color: rgb(255, 255, 255);
					margin-bottom: 0;
					font-weight: 400; } } } }
	@media screen and(max-width:1600px) {
		.banner-row-wrapper {
			.pad-mobile {
				padding-right: 0; }
			.figure {
				.figure-image {
					height: 4rem;
					width: 4rem;
					min-width: 4rem; }
				.figure-caption {
					margin-left: .5rem;
					p {
						font-size: r(15px);
						letter-spacing: r(15px*.0625);
						line-height: r(23px); } } } } }
	@media screen and(max-width:1200px) {
		.banner-row-wrapper {
			.figure {
				margin-bottom: 1rem; } } }
	@media screen and(max-width:768px) {
		.banner-row-wrapper {
			.figure {
				justify-content: flex-start; } } }
	@media screen and(max-width:576px) {
		.banner-row-wrapper {
			.pad-mobile {
				padding-left: r(10px); }
			.figure {
				.figure-image {
					height: 3rem;
					width: 3rem;
					min-width: 3rem;
					img {
						width: 50%; } }
				.figure-caption {
					p {
						font-size: r(12px);
						letter-spacing: r(12px*.0625);
						line-height: r(20px); } } } } } }
