.news-sidebar {
	margin-bottom: 2rem;
	.card-new-wrapper {
		background: #f6f6f6; }
	.news-image {
		margin-top: 2rem;
		img {
			max-width: 100%;
			height: auto; }
		@media screen and(max-width:992px) {
			img {
				width: 100%; } } }
	.sidebar-index-wrappper {
		list-style-type: none;
		margin: 0;
		padding: 0;
		li {

			background: #fffcf7;
			&.active,&:hover {
				a {
					font-weight: 700;
					color: #ed1c24; }
				ul {
					li {
						a {
							font-weight: 400; } } } }
			&.have-children {
				position: relative;
				&:hover {
					ul {
						li {
							a {
								border-bottom: none; }
							&:hover {
								background: #fafafa; } } } } }
			a {
				border-bottom: 1px solid rgba(#edebe6,1);
				margin:  0 2rem;
				position: relative;
				display: block;
				color: #666666;
				text-decoration: none;
				text-transform: uppercase;
				padding: r(14px)  0;
				font-size: r(20px);
				font-family: "Muli";
				font-weight: bold; }
			ul {
				list-style-type: none;
				display: none;
				margin: 0;
				padding: 0;
				li {
					padding: 0 2rem;
					border-bottom: 1px solid rgba(#fafafa,1); } } }
		@media (max-width:1600px) {
			li {
				a {
					margin:  0 1rem;
					font-size: r(14px);
					padding: r(10px) 0;
					line-height: r(20px); } } }
		@media (max-width:1200px) {
			li {
				a {
					margin:  0 .5rem;
					font-size: r(14px);
					padding: r(8px) 0;
					line-height: r(20px); }
				ul {
					li {
						padding: 0 1rem; } } } }
		@media (max-width:992px) {
			display: none;
			li {
				ul {
					li {
						padding: 0 1rem; } }
				a {
					margin: 0 1rem; } } } }
	.sidebar-toggle {
		padding: 14px 0;
		display: flex;
		align-items: center;
		color: #533d0d;
		background: url('../images/options-1/bg-linear-gradient-gold-small.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		-webkit-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
		-moz-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
		box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
		.mdi {
			font-size: 24px;
			width: 36px;
			height: 36px;
			display: flex;
			justify-content: center;
			align-items: center;
			line-height: 1; }
		.text {
			text-transform: uppercase;
			display: block;
			margin-left: r(15px);
			font-size: r(24px);
			font-family: "Muli";
			color: rgb(83, 61, 13);
			font-weight: 900;
			line-height: 1.269; }
		@media screen and(max-width:1600px) {
			.text {
				font-size: 16px; } }
		@media (max-width: 1200px) {
			padding: 10px 0;
			.text {
				font-size: 14px; } }
		@media (max-width: 992px) {
			.text {
				font-size: 18px; } }
		@media (max-width: 768px) {
			padding: 7px 0; }

		@media (max-width: 768px) {
			.text {
				font-size: 16px; } } }
	.desktop-title-2 {
		padding: 14px 0;
		display: flex;
		align-items: center;
		color: #533d0d;
		background: url('../images/options-1/bg-linear-gradient-gold-small.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		-webkit-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
		-moz-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
		box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
		.mdi {
			font-size: 24px;
			width: 36px;
			height: 36px;
			display: flex;
			justify-content: center;
			align-items: center;
			line-height: 1; }
		.text {
			text-transform: uppercase;
			display: block;
			margin-left: r(32px);
			font-size: r(24px);
			font-family: "Muli";
			color: rgb(83, 61, 13);
			font-weight: 900;
			line-height: 1.269; }
		@media (max-width: 1600px) {
			padding: 10px 0;
			.text {
				font-size: 16px; } }
		@media (max-width: 1200px) {
			padding: 10px 0;
			.text {
				font-size: 14px;
				margin-left: 1rem;
				white-space: nowrap; } }

		@media (max-width: 768px) {
			.text {
				font-size: 16px; } } }

	.card-news {
		padding: 1rem 0;
		display: flex;
		justify-content: center;
		align-items: center;
		margin:  0 2rem;
		&:not(:last-child) {
			border-bottom: 1px solid #edebe6; }
		&:last-child {
			padding-bottom: 2rem; }
		.card-image {
			margin-right: 25px;

			img {
				max-width: 100%;
				height: auto; } }
		.card-caption {

			p {

				margin: 0;
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: r(20px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				font-weight: bold;
				line-height: r(25px); } }
		@media screen and(max-width:1600px) {
			margin:  0 1rem;
			.card-image {
				margin-right: r(10px);
				img {
					min-width: 3.5rem; } }
			.card-caption {
				p {
					font-size: r(18px);
					line-height: r(23px); } } }
		@media screen and(max-width:1200px) {

			.card-caption {
				p {
					font-size: r(16px);
					line-height: r(22px); } } }
		@media screen and(max-width:992px) {
			.card-caption {
				p {
					font-size: r(16px);
					line-height: r(22px); } } } } }




