.map-showroom {
	margin: r(40px) 0;
	.map-showroom-wrapper {
		padding: r(50px) 0 r(40px) 0;
		background: url('../images/options-1/bg-linear-gradient-gold-map-tablet.png');
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		.showroom-map {
			img {}
			@media screen and(max-width: 992px) {
				display: flex;
				justify-content: center;
				align-items: center; }
			@media screen and(max-width:768px) {
				padding: 0 2rem;
				img {
					width: 100%;
					max-width: 100%;
					height: auto; } } }
		.address-title {
			text-align: center;
			span {
				font-size: r(18px);
				text-align: center;
				font-family: 'Muli';
				color: rgb(102, 102, 102);
				line-height: r(26px); }
			h2 {
				font-size: r(30px);
				font-family: 'Muli';
				color: rgb(237, 28, 36);
				line-height: r(38px);
				font-weight: 900; }
			i {
				display: none; } }
		.map-showroom-nav {
			padding: r(15px) 0 r(15px) 0;
			.button-prev-map,.button-next-map {
				display: none; }
			.swiper-container {
				width: r(600px);
				margin: 0 0;
				display: flex;
				justify-content: flex-start;
				align-items: center;
				height: r(50px);
				position: relative;
				.swiper-wrapper {
					width: 100%; }
				.swiper-slide {
					padding: 1rem 0;
					margin: 0 .5rem 0 0;
					width: auto;
					overflow: auto;
					.pad-0 {
						padding: 0 1rem 0 0; }
					&.swiper-slide-thumb-active {
						a {
							background: #EE1C25;
							position: relative;
							color: #533d0d;
							background: url('../images/options-1/bg-linear-gradient-gold-small.png');
							background-size: cover;
							background-position: center;
							background-repeat: no-repeat;
							-webkit-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							-moz-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							box-shadow: 3px 3px 0px 0px rgba(111,50,5,1); } }
					&:not(:last-child) {
						a {
							margin-right: r(10px); } }
					a {
						padding: .75rem 1rem;
						text-transform: uppercase;
						text-decoration: none;
						background: #EC1D25;
						color: #fff;
						font-family: 'Muli';
						font-size: r(16px);
						line-height: r(24px);
						font-weight: 700; } } } }
		.address-wrapper {
			.swiper-container {
				.scrollbar-wrapper {
					padding: 0 r(20px) 0 0; }
				.overflow-scrollbar {
					overflow: auto;
					height: r(450px);
					width: 100%;
					scrollbar-color: #de1f25 #fff;
					scrollbar-width: thin;
					&::-webkit-scrollbar {
						margin-right: r(20px);
						width: r(4px);
						height: r(100px);
						margin-top: r(20px); }
					&::-webkit-scrollbar-track {
						box-shadow: inset 5px 5px 5px 5px #fff;
						border-radius: r(20px); }
					&::-webkit-scrollbar-thumb {
						background: #de1f25;
						overflow: hidden;
						&:hover {
							background: red; } }

					.card {
						margin-top: .5rem;
						h3 {
							margin-bottom: 0;
							color: #333333;
							font-size: r(16px);
							text-transform: uppercase;
							font-family: 'Muli';
							line-height: r(24px);
							font-weight: bold; }
						p {
							padding: 0 2rem 0 0;
							margin-bottom: 0;
							color: #333333;
							font-size: r(14px);
							font-family: 'Muli';
							line-height: r(22px); } } }
				@media screen and(max-width:992px) {
					.scrollbar-wrapper {
						padding: 0 r(20px) 0 r(20px);
						.card {
							h3 {
								font-size: r(14px); } } } }
				@media screen and(max-width:576px) {
					.overflow-scrollbar {
						.card {
							p {
								padding: 0 0; } } } } } }
		.mobile-text {
			display: none;
			justify-content: center;
			align-items: center;
			p {
				font-size: r(15px);
				font-family: "Muli";
				color: rgb(83, 61, 13);
				line-height: r(23px);
				line-height: 1.2;
				text-align: center;
				font-weight: 500; } }
		.mobile-button {
			display: none;
			justify-content: center;
			align-items: center;
			a {
				text-decoration: none;
				display: flex;
				justify-content: center;
				align-items: center;
				background: rgb(237, 28, 36);
				color: #fff;
				text-transform: uppercase;
				font-size: r(14px);
				font-family: "Muli"; } } }

	@media screen and(max-width:1600px) {
		margin: r(30px) 0; }

	@media screen and(max-width:1200px) {
		.map-showroom-wrapper {
			.center {
				display: flex; }
			.showroom-map {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-left: 2rem;
				img {
					max-width: 100%;
					height: auto; } } } }

	@media screen and(max-width:991.9px) {
		.map-showroom-wrapper {
			.showroom-map {
				display: none; } } }
	@media screen and(max-width:992px) {
		margin: r(25px) 0;
		.map-showroom-wrapper {
			.address-title {
				h2 {
					font-size: r(26px);
					line-height: r(32px); } }

			.map-showroom-nav {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-left: 6.5rem;
				.swiper-container {
					.swiper-slide {
						&:last-child {}
						margin-right: 0;
						a {
							font-size: r(14px);
							line-height: r(22px); } } } } } }

	@media screen and(max-width:768px) {
		margin: r(20px) 0;
		.map-showroom-wrapper {
			background: url('../images/options-1/bg-linear-gradient-gold-map-mobile.png');
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			padding: .75rem 0 2.5rem 0;
			.address-title {
				display: flex;
				justify-content: center;
				align-items: center;
				flex-flow: column;
				font-size: r(24px);
				span {
					display: none; }
				i {
					display: flex;
					font-size: 4rem;
					color: rgb(237, 28, 36); } }
			.map-showroom-nav {
				display: none; }
			.address-wrapper {
				display: none; }
			.mobile-button {
				display: flex;
				a {
					font-weight: 900;
					padding: 0.6rem 1rem; } }
			.mobile-text {
				display: flex;
				p {
					font-weight: 600;
					padding: 0 1rem;
					font-size: r(14px);
					line-height: r(22px); } } } }
	@media screen and(max-width:576px) {
		.map-showroom-wrapper {
			.map-showroom-nav {
				display: none; } } } }
