.kingsport-news-option1v3 {
	.news-wrapper {
		.news-title {
			text-align: center;
			h2 {
				font-size: r(36px);
				color: #EC1F26;
				font-family: 'Muli';
				line-height: r(44px);
				font-weight: 900; } }
		.view-more {
			border-bottom: 1px solid #EC1F26;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: r(10px);
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				color: #EC1F26;
				font-size: r(18px);
				font-family: 'Muli';
				line-height: r(24px);
				font-weight: 700; } }
		.select-news-mobile-options {
			font-family: 'Muli';
			font-size: r(16px);
			font-weight: 700;
			text-decoration: none;
			z-index: 99;
			margin-bottom: 2rem;
			list-style-type: none;
			padding: 0.4em 0;
			background: #ed1c24;
			line-height: r(24px);
			color: #fff;
			text-align: left;
			border: 1px solid #ed1c24;
			cursor: pointer;
			width: 100%;
			display: none;
			justify-content: center;
			flex-direction: row-reverse;
			position: relative;
			&:before {
				font-family: 'Material Design Icons';
				content: '\f35d';
				font-size: 1rem;
				position: absolute;
				right: 1rem;
				color: #fff; }
			&.open {
				&:before {
					content: '\f360'; } }
			.default-footer-select {
				width: 100%;
				margin: 0 0;
				padding: 0 0;
				text-indent: 1rem;
				position: relative;
				list-style-type: none;
				.default-select-options {}

				.options {
					display: block;
					list-style-type: none;
					padding: 0;
					margin: 0 0;
					background: transparent;
					position: absolute;
					width: 100%;
					max-height: 0px;
					overflow: hidden;
					border: 3px solid transparent;
					top: 2rem;
					left: 0;
					transition: all ease 0.25s;
					li {
						padding: 0.4rem 0;
						border-bottom: 1px solid rgba(#2F2F3F,0.4);
						&:hover {
							background:rgba(#2F2F3F,0.2) {} }
						&:last-of-type {
							border-bottom: 0; } }
					&.open {
						background: rgba(#ed1c24,.8);
						max-height: 1000%;
						width: 100%;
						border: 1px solid #ed1c24;
						border-top: 3px solid transparent; } } }

			@media screen and(max-width:768px) {
				display: flex;
				margin: 1rem 0 0 0; } }
		.news-tab-nav {
			display: flex;
			justify-content: center;
			align-items: center;
			ul {
				display: flex;
				list-style-type: none;
				overflow: hidden;
				width: 100%;
				justify-content: center;
				padding: 0.8rem 0 r(8px) 0;
				margin-bottom: 1.3rem;
				li {
					&:not(:last-child) {
						margin-right: 1rem; }
					&.active {
						a {
							color: #533d0d;
							background: url('../images/options-1/bg-linear-gradient-gold-small.png');
							background-size: cover;
							background-position: center;
							background-repeat: no-repeat;
							-webkit-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							-moz-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							box-shadow: 3px 3px 0px 0px rgba(111,50,5,1); } }
					a {
						display: flex;
						justify-content: center;
						align-items: center;
						padding: .65rem 2rem;
						text-decoration: none;
						color: #fff;
						background: #EC1D25;
						font-family: 'Muli';
						font-size: r(16px);
						line-height: r(24px);
						font-weight: 900; } } } } }

	.news-box {
		.news-banner {
			img {
				width: 100%; } }
		.tab-content-news {
			.tab-panel-news {
				width: 100%;
				height: 0;
				opacity: 0;
				&.active {
					height: 100%;
					opacity: 1; } } }
		.swiper-pagination-wrapper {
			display: none;
			justify-content: center;
			align-items: center;
			.swiper-pagination {
				.swiper-pagination-bullet {
					background: #EC1F26;
					opacity: 1; }
				.swiper-pagination-bullet-active {
					background: -moz-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
					background: -webkit-gradient(linear, left bottom, right top, color-stop(0%, rgba(255,0,0,1)), color-stop(50%, rgba(255,255,0,1)), color-stop(100%, rgba(255,0,0,1)));
					background: -webkit-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
					background: -o-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
					background: -ms-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
					background: linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
					filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff0000', endColorstr='#FF0000',GradientType=1 ); }
				span {
					&:not(:last-child) {
						margin-right: .425rem; } } } }
		.swiper-container {
			position: relative;
			&:hover {
				.button-prev-news,.button-next-news {
					opacity: 1; }
				.swiper-button-disabled {
					opacity: .3; } }
			.button-prev-news,.button-next-news {
				background: #ed1c24;
				opacity: 1;
				outline: none;
				width: r(35px);
				height: r(35px);
				display: flex;
				justify-content: center;
				align-items: center;
				position: absolute;
				z-index: 99;
				top: 50%;
				transform: translateY(-50%);
				opacity: 0;
				-webkit-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
				-moz-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
				-o-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
				transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
				-webkit-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
				-moz-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
				-o-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
				transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
				i {
					color: #fff;
					font-size: 1.5rem; }
				&:hover {
					border-radius: 50%; } }
			.swiper-button-disabled {
				opacity: 0;
				cursor: auto;
				pointer-events: auto; }
			.button-prev-news {
				left: 0; }
			.button-next-news {
				right: 0; }

			.swiper-wrapper {
				.swiper-slide {
					.figure {
						.figure-image {
							img {
								width: 100%; } }
						.figure-caption {
							h4 {
								font-size: r(18px);
								line-height: r(24px);
								font-family: 'Muli';
								color: rgb(51, 51, 51);
								font-weight: bold; }
							h5 {
								font-size: r(14px);
								font-weight: 400;
								font-family: 'Muli';
								color: #ed1c24;
								font-weight: 400;
								line-height: r(22px); }
							p {
								font-size: r(16px);
								line-height: r(24px);
								font-weight: 400;
								font-family: 'Muli';
								color: rgb(102, 102, 102); } } } } } } }

	@media screen and(max-width: 1919px) {
