						a {
							font-size: r(11px);
							font-weight: 700;
							padding: .65rem .5rem; } } } } } }
	@media screen and(max-width: 992px) {
		.news-wrapper {
			.news-title {
				h2 {
					font-size: r(28px);
					color: #EC1F26;
					font-family: 'Muli';
					line-height: r(36px); } }
			.view-more {
				margin-top: 1rem; }
			.news-tab-nav {
				display: none; }
			.select-news-mobile-options {
				display: flex;
				margin-bottom: 1rem;
				.select-news-wrapper {
					width: 100%; } } }
		.news-box {
			.swiper-container {
				.button-prev-news,.button-next-news {
					display: none; }
				.swiper-wrapper {
					.swiper-slide {
						.figure {
							.figure-caption {
								h4 {
									height: 3rem;
									font-size: r(16px);
									line-height: r(24px);
									overflow: hidden; }
								h5 {
									font-size: r(14px);
									line-height: r(21px); }
								p {
									height: 3.8rem;
									overflow: hidden;
									font-size: r(14px);
									line-height: r(21px); } } } } } }
			.d-md {
				display: none;
				@media screen and(min-width:768px) {
					display: block; } }
			.news-banner {
				margin-top: .8rem;
				margin-bottom: 1rem;
				& {} }
			.swiper-pagination-wrapper {
				margin-top: 1rem;
				display: flex; } } }


	@media screen and(max-width: 576px) {
		.news-box {
			.swiper-container {
				.swiper-wrapper {
					.swiper-slide {
						.figure {
							.figure-caption {
								h4 {
									line-height: r(16px);
									height: 2rem; }
								h5 {
									line-height: r(20px); }
								p {
									line-height: r(20px); } } } } } } } } }
