.shopping-cart-check-out {
	padding-bottom: 50px;
	.previous-page {
		a {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			text-decoration: none; }
		em {
			font-size: 1.5rem;
			display: inline;
			color: #999999;
			height: 2rem; }
		p {
			margin-bottom: 0;
			font-size: r(20px);
			font-family: "Muli";
			color: rgb(177, 123, 20);
			line-height: r(27px); } }

	.cart-item-wrapper {
		border: 1px solid #ebebeb; }
	.cart-item-information-wrapper {
		padding: 2rem 0;
		margin: 0 2rem;
		border-bottom: 1px solid #ebebeb;
		display: flex;
		justify-content: space-between;
		align-items: flex-start; }
	.cart-image {
		img {
			max-width: 100%;
			height: auto; }
		.remove-item {
			display: flex;
			justify-content: center;
			align-items: center;
			a {
				text-decoration: none;
				h5 {
					position: relative;
					font-size: r(20px);
					font-family: "Muli";
					color: rgb(102, 102, 102);
					margin: 0;
					padding: 0;
					&:before {
						display: flex;
						justify-content: center;
						align-items: center;
						position: absolute;
						bottom: 0;
						left: -2.5rem;
						flex-flow: wrap-reverse;
						content: "x";
						width: 2rem;
						height: 2rem;
						border-radius: 50%;
						background:  rgb(237, 28, 36);
						color: #fff;
						font-size: 1.5rem; } } } } }
	.cart-item-data {
		h2 {
			font-size: r(24px);
			font-family: "Muli";
			color: rgb(237, 28, 36);
			font-weight: bold;
			line-height: r(32px);
			text-transform: uppercase; }
		p {

			margin-bottom: .5rem;
			font-size: 20px;
			font-family: "Muli";
			color: rgb(102, 102, 102);
			line-height: r(26px);
			span {
				font-size: 20px;
				font-family: "Muli";
				color: rgb(237, 28, 36);
				line-height: r(26px);
				font-weight: 600;
				a {
					margin-left: .5rem;
					font-size: 20px;
					font-family: "Muli";
					color: rgb(237, 28, 36);
					line-height: r(26px);
					font-weight: 400; } } } }
	.cart-price {
		display: flex;
		justify-content: space-between;
		flex-flow: column;
		align-items: center;
		h2 {
			font-size: r(24px);
			font-family: "Muli";
			color: rgb(237, 28, 36);
			font-weight: 800;
			span {
				font-size: r(24px);
				font-family: "Muli";
				color: rgb(237, 28, 36);
				font-weight: 800; } }
		.cart-item-quantity {
			.input-group {
				display: flex;
				justify-content: flex-start;
				align-items: center; }
			.input-group-btn {
				border: 1px solid #ebebeb;
				display: flex;
				justify-content: center;
				align-items: center;
				background: none;
				height: r(50px);
				width: r(60px);
				position: relative;
				&:hover {
					background: none;
					color: rgb(237, 28, 36);
					transition: .25s all ease-in-out; }
				button {
					z-index: 5;
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					cursor: pointer;
					opacity: 0;
					background: #f5f5f5;
					border: none;
					em {
						color: #c5c5c5; } } }
			input {
				border: 1px solid #ebebeb;
				text-align: center;
				background: #ffffff;
				width: r(60px);
				height: r(50px); } } }
	.cart-item-price {
		padding: 1.5rem 2rem;
		border-bottom: 2px solid #b7b7b7;
		.sum-1 {
			display: flex;
			justify-content: space-between;
			align-items: center;
			p {
				font-size: r(20px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				line-height: 1.2;
				text-align: left;
				span {
					font-size: r(20px);
					font-family: "Muli";
					color: rgb(51, 51, 51);
					line-height: 1.2;
					text-align: left; } } }
		.sum-2 {
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			h3 {
				font-size: r(24px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				font-weight: bold;
				line-height: r(32px);
				text-align: left; }
			.sum-right {
				display: flex;
				flex-flow: column;
				justify-content: center;
				align-items: flex-end;
				h2 {
					font-size: r(24px);
					font-family: "Muli";
					color: rgb(237, 28, 36);
					font-weight: bold;
					line-height: r(32px);
					span {
						font-size: r(24px);
						font-family: "Muli";
						color: rgb(237, 28, 36);
						font-weight: bold;
						line-height: r(32px); } }
				h5 {
					font-size: r(20px);
					font-family: "Muli";
					color: rgb(177, 123, 20);
					font-weight: bold;
					line-height: 1.4; } } }
		.discount-code {
			margin-top: 1rem;
			display: flex;
			justify-content: center;
			align-items: center;
			.form-group {
				input {
					border-radius: 3px;
					border: 1px solid rgb(153, 153, 153);
					padding: .3rem .5rem;
					font-size: r(18px);
					font-family: "Muli";
					color: #333333;
					line-height: r(27px);
					&:focus {
						outline: none; }
					&::placeholder {
						font-size: r(20px);
						font-family: "Muli";
						color: rgb(153, 153, 153);
						line-height: r(27px); } } }
			.discount-button {
				margin-left: .5rem;
				a {
					text-decoration: none;
					display: flex;
					justify-content: center;
					align-items: center;
					font-size: r(24px);
					font-family: "Muli";
					color: rgb(83, 61, 13);
					padding: .25rem 1rem;
					font-weight: bold;
					line-height: r(32px);
					border-radius: 3px;
					background: -moz-linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%);
					background: -webkit-linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%);
					background: -ms-linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%);
					background: linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%); } }

			.discount-button {
				a {
					text-decoration: none; } } } }
	.cart-customer-information {
		padding: 20px 2rem;
		.customer-gender {
			padding-bottom: 1rem;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			.form-check {
				margin-right: 1rem;
				position: relative;
				height: 100%;
				position: relative;
				display: inline-flex;
				input {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					z-index: 5;
					opacity: 0;
					cursor: pointer;
					height: 100%;
					&:checked + label {
						&:before {
							content: '\f43e';
							color: #cdcdcd; } } }
				label {
					white-space: nowrap;
					color: #333;
					margin: 0;
					padding: 0;
					font-size: r(22px);
					font-family: "Muli";
					line-height: 1.091;
					position: relative;
					display: inline-block;
					&:before {
						font-family: 'Material Design Icons';
						content: '\f43d';
						font-size: 1.5rem;
						margin-right: .5rem;
						color: #cdcdcd; } } } }
		.customer-form {
			h5 {
				padding:  1rem 0;
				font-size: r(20px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				font-weight: bold;
				line-height: r(26px);
				span {
					font-weight: 400; } }
			.form-check {
				margin-right: 1rem;
				height: 100%;
				position: relative;
				display: inline-flex;
				position: relative;
				input {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					opacity: 0;
					z-index: 5;
					cursor: pointer;
					&:checked ~ label {
						&:before {
							content: '\f43e';
							color: #cdcdcd; } } }
				label {
					white-space: nowrap;
					color: rgb(177, 123, 20);
					font-weight: 600;
					margin: 0;
					padding: 0;
					font-size: r(22px);
					font-family: "Muli";
					line-height: 1.091;
					position: relative;
					display: inline-block;
					&:before {
						font-family: 'Material Design Icons';
						content: '\f43d';
						font-size: 1.5rem;
						color: #cdcdcd;
						margin-right: .5rem; } } }

			.mt {
				margin-top: 1rem; }
			.form-group {
				width: 100%;
				input {
					border-radius: 3px;
					border: 1px solid #cccccc;
					padding: .3rem 2rem;
					width: 100%;
					font-size: r(20px);
					font-family: "Muli";
					color: #333333;
					line-height: r(28px);
					&::placeholder {
						font-size: r(20px);
						font-family: "Muli";
						color: rgb(153, 153, 153);
						line-height: r(28px); }


					&:focus {
						outline: none; } } } }
		.cart-submit {
			padding-top: 3rem;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			.yellow {
				background: -moz-linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%);
				background: -webkit-linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%);
				background: -ms-linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%);
				background: linear-gradient( 90deg, rgb(207,141,50) 0%, rgb(207,141,50) 6%, rgb(251,175,67) 11%, rgb(255,232,100) 16%, rgb(224,177,88) 24%, rgb(255,255,192) 47%, rgb(207,140,50) 71%, rgb(206,138,47) 79%, rgb(255,182,61) 85%, rgb(206,131,40) 90%, rgb(217,147,48) 100%);
				a {
					color: rgb(83, 61, 13); } }
			.red {
				background: rgb(237, 28, 36);
				a {
					color: #ffffff; } }

			.submit-button {
				margin-right: 1rem;
				border-radius: 3px;
				a {
					text-decoration: none;
					display: flex;
					justify-content: center;
					flex-flow: column;
					align-items: center;
					font-size: r(24px);
					font-family: "Muli";
					padding: .35rem 1rem;
					font-weight: bold;
					line-height: r(32px);
					h2 {
						font-size: r(20px);
						font-family: "Muli";
						text-transform: uppercase;
						line-height: r(28px);
						font-weight: 900;
						margin-bottom: 0; }
					p {

						font-size: 18px;
						margin-bottom: 0;
						font-weight: 400;
						font-family: "Muli"; } } } } }
	@media screen and(max-width:1600px) {}
	@media screen and(max-width:1400px) {}
	@media screen and(max-width:1200px) {
		padding-bottom: 30px;
		.cart-item-data {
			h2 {
				font-size: r(20px);
				line-height: r(27px); }
			p {
				font-size: r(18px);
				line-height: r(25px);
				span {
					font-size: r(18px);
					line-height: r(25px);
					a {
						font-size: r(18px);
						line-height: r(25px); } } } } }

	@media screen and(max-width:992px) {
		.cart-item-information-wrapper {
			flex-flow: column;
			align-items: center; }
		.cart-item-data {
			width: 100%; }
		.cart-image {
			padding-bottom: 2rem; }
		.cart-price {
			width: 100%;
			flex-flow: row;
			justify-content: space-between; }
		.cart-customer-information {
			.customer-form {
				.form-group {
					margin-top: 1rem; } }
			.cart-submit {
				padding-top: 2rem;
				.submit-button {
					a {
						h2 {
							font-size: r(18px);
							line-height: r(25px); } } } } } }
	@media screen and(max-width:768px) {
		padding-bottom: 20px;
		.cart-item-information-wrapper {
			margin:  0 1rem; }
		.cart-item-data {
			h2 {
				font-size: r(18px);
				line-height: r(25px); }
			p {
				font-size: r(16px);
				line-height: r(22px);
				span {
					font-size: r(16px);
					line-height: r(22px);
					a {
						font-size: r(16px);
						line-height: r(22px); } } } }
		.cart-customer-information {
			padding: 20px 1rem;
			.cart-submit {
				.submit-button {
					a {
						h2 {
							white-space: nowrap;
							font-size: r(14px);
							line-height: r(23px); }
						p {
							text-align: center; } } } } }
		.cart-item-price {
			padding: 1.5rem 1rem;
			.sum-2 {
				h3 {
					white-space: nowrap; }
				.sum-right {
					margin-left: -2rem; } }
			.discount-code {
				.discount-button {
					white-space: nowrap; } } } }

	@media screen and(max-width:576px) {
		.cart-item-information-wrapper {
			.cart-price {
				flex-flow: column;
				align-items: flex-start; } }
		.cart-item-price {
			.sum-2 {
				flex-flow: column;
				.sum-right {
					align-items: flex-start;
					margin: 0;
					.discount-code {
						margin-top: .5rem;
						flex-flow: column;
						align-items: flex-start;
						.form-group {
							margin-bottom: 1rem;
							input {
								font-size: r(16px);
								&::placeholder {
									font-size: r(16px); } } }
						.discount-button {
							margin: 0;
							a {
								font-size: r(18px); } } } } } }
		.cart-customer-information {
			.customer-gender {
				.form-check {
					label {
						font-size: r(16px); } } }
			.customer-form {
				h5 {
					font-size: r(18px);
					padding: 1rem 0 .5rem 0; }
				.form-group {
					input {
						font-size: r(16px);
						&::placeholder {
							font-size: r(16px); } } }
				.form-check {
					margin-bottom: .5rem;
					label {
						font-size: r(16px); } } }
			.cart-submit {
				flex-flow: column;
				.submit-button {
					width: 100%;
					margin: 0 0 1rem 0;


					a {
						width: 100%; } } } } }

	@media screen and(max-width:400px) {} }
