.mobile-nav {
	padding-top: r(20px);
	.swiper-container {
		.swiper-slide {
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				flex-flow: column;
				text-decoration: none;
				img {
					width: r(42px); }
				.swiper-slide-caption {
					p {
						color: #323232;
						font-size: r(13px);
						font-family: 'Muli';
						letter-spacing: r(13px*.0125);
						line-height: r(21px);
						font-weight: 600;
						text-align: center;
						margin-bottom: 0;
						&::first-letter {
							text-transform: uppercase; } } } } } }
	@media screen and(min-width:992.1px) {
		display: none; } }
