.product-today {
	padding: r(50px) 0 0 0;
	.product-wrapper {
		border-bottom: 1px solid #EC1D25;
		.view-more {
			display: flex;
			justify-content: center;
			align-items: center;
			padding: r(10px);
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				color: #EC1F26;
				font-size: r(18px);
				font-family: 'Roboto';
				line-height: r(24px);
				font-weight: 700; } }
		.product-title {
			text-align: center;
			span {
				font-size: r(18px);
				color: #656565;
				font-family: 'Roboto';
				line-height: r(23px); }
			h2 {
				font-size: r(36px);
				color: #EC1F26;
				font-family: 'Roboto';
				line-height: r(44px);
				font-weight: 900; } }
		.multi-row-product-today {
			.swiper-pagination-wrapper {
				display: flex;
				justify-content: center;
				align-items: center;
				.swiper-pagination {
					.swiper-pagination-bullet {
						background: #EC1F26;
						opacity: 1; }
					.swiper-pagination-bullet-active {
						background: -moz-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -webkit-gradient(linear, left bottom, right top, color-stop(0%, rgba(255,0,0,1)), color-stop(50%, rgba(255,255,0,1)), color-stop(100%, rgba(255,0,0,1)));
						background: -webkit-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -o-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -ms-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff0000', endColorstr='#FF0000',GradientType=1 ); }
					span {
						&:not(:last-child) {
							margin-right: .425rem; } } } }
			.swiper-container {
				.swiper-wrapper {
					.swiper-slide {
						a {
							text-decoration: none;
							.figure {
								display: flex;
								justify-content: center;
								align-items: center;
								.figure-image {}
								.figure-caption {
									padding: 0.5rem 1rem;
									span {
										font-size: r(14px);
										line-height: r(22px);
										font-family: 'Muli';
										color: rgb(102, 102, 102);
										font-weight: 400; }
									h4 {
										font-size: r(16px);
										font-family: 'Muli';
										color: rgb(51, 51, 51);
										font-weight: bold; }
									h5 {
										font-size: r(16px);
										font-family: 'Muli';
										color: rgb(237, 28, 36);
										font-weight: bold;
										span {
											font-size: r(16px);
											font-family: 'Muli';
											color: rgb(237, 28, 36);
											font-weight: 400; } }
									p {
										font-size: r(14px);
										line-height: r(22px);
										font-family: 'Muli';
										color: rgb(102, 102, 102);
										font-weight: 400; } } } } } } } } }
	@media screen and(max-width:1600px) {
		.product-wrapper {
			.product-title {
				h2 {
					font-size: r(28px);
					line-height: 1.2; } }
			.multi-row-product-today {
				.swiper-container {
					.swiper-wrapper {
						.swiper-slide {
							a {
								.figure {
									.figure-image {}
									.figure-caption {
										span {
											margin-bottom: .4rem; }
										h4 {
											margin-bottom: .4rem; }
										h5 {
											margin-bottom: .4rem; }
										p {
											margin-bottom: .4rem; } } } } } } } } } }
	@media screen and(max-width:992px) {
		.product-wrapper {
			.view-more {
				a {
					font-size: r(16px);
					line-height: r(24px); } }
			.product-title {
				h2 {
					font-size: r(24px);
					line-height: 1.2; } }
			.multi-row-product-today {
				.swiper-container {
					.swiper-wrapper {
						.swiper-slide {
							a {
								.figure {
									.figure-image {}
									.figure-caption {
										span {
											font-size: r(13px);
											line-height: r(21px);
											margin-bottom: .4rem; }
										h4 {
											font-size: r(14px);
											line-height: r(21px);
											margin-bottom: .4rem; }
										h5 {
											font-size: r(13px);
											line-height: r(21px);
											margin-bottom: .4rem; }
										p {
											font-size: r(13px);
											line-height: r(21px);
											margin-bottom: .4rem; } } } } } } } } } }
	@media screen and(max-width:768px) {
		.product-wrapper {
			.multi-row-product-today {
				.swiper-pagination-wrapper {}
				.swiper-container {
					.swiper-slide {
						a {
							.figure {
								.figure-image {}
								.figure-caption {
									span {
										margin-bottom: .4rem; }
									h4 {
										margin-bottom: .4rem; }
									h5 {
										margin-bottom: .4rem; }
									p {
										margin-bottom: .4rem; } } } } } } } } }
	@media screen and(max-width:576px) {
		.product-wrapper {
			.product-title {
				h2 {
					font-size: r(20px);
					line-height: 1.2; } } } }
	@media screen and(max-width:400px) {} }
