.product-recently {
	padding: r(50px) 0;
	.product-wrapper {
		border-bottom: 1px solid #EC1D25;

		.product-recently-title {
			text-align: center;
			h2 {
				font-size: r(36px);
				color: #EC1F26;
				font-family: 'Muli';
				line-height: r(44px);
				font-weight: 900; }
			@media screen and(max-width:1919px) {
				h2 {
					font-size: r(32px);
					line-height: r(40px); } }
			@media screen and(max-width:1600px) {
				h2 {
					font-size: r(28px);
					line-height: r(36px); } }
			@media screen and(max-width:992px) {
				h2 {
					font-size: r(24px);
					line-height: r(32px);
					margin-bottom: 1rem; } }
			@media screen and(max-width:576px) {
				h2 {
					font-size: r(20px);
					line-height: r(28px); } } }
		.product-recently-wrapper {
			.swiper-pagination-wrapper {

				display: none;
				justify-content: center;
				align-items: center;
				.swiper-pagination {
					.swiper-pagination-bullet {
						background: #EC1F26;
						opacity: 1; }
					.swiper-pagination-bullet-active {
						background: -moz-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -webkit-gradient(linear, left bottom, right top, color-stop(0%, rgba(255,0,0,1)), color-stop(50%, rgba(255,255,0,1)), color-stop(100%, rgba(255,0,0,1)));
						background: -webkit-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -o-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -ms-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff0000', endColorstr='#FF0000',GradientType=1 ); }
					span {
						&:not(:last-child) {
							margin-right: .425rem; } } } }
			.swiper-container {
				position: relative;
				&:hover {
					.button-prev-recent,.button-next-recent {
						opacity: 1; }
					.swiper-button-disabled {
						opacity: .3; } }
				.button-prev-recent,.button-next-recent {
					background: #ed1c24;
					opacity: 1;
					outline: none;
					width: r(35px);
					height: r(35px);
					display: flex;
					justify-content: center;
					align-items: center;
					position: absolute;
					z-index: 99;
					top: 50%;
					transform: translateY(-50%);
					opacity: 0;
					-webkit-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
					-moz-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
					-o-transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
					transition: all 200ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
					-webkit-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
					-moz-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
					-o-transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
					transition-timing-function: cubic-bezier(0.420, 0.000, 0.580, 1.000);
					i {
						color: #fff;
						font-size: 1.5rem; }
					&:hover {
						border-radius: 50%; } }
				.swiper-button-disabled {
					opacity: 0;
					cursor: auto;
					pointer-events: auto; }
				.button-prev-recent {
					left: 0; }
				.button-next-recent {
					right: 0; }
				.swiper-wrapper {
					margin: 1px 0 0 1px;

					.swiper-slide {
						a {
							text-decoration: none; }
						.swiper-item {
							max-width: r(327px);
							position: relative;
							.swiper-image {
								position: relative;
								height: r(270px);
								width: auto;
								img {
									width: 100%;
									height: 100%;
									object-fit: scale-down; } }
							@include swiper-caption-recently; } } } } }

		.view-more {
			display: flex;
			justify-content: center;
			align-items: center;
			padding: r(10px);
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				color: #EC1F26;
				font-size: r(18px);
				font-family: 'Muli';
				line-height: r(24px);
				font-weight: 700; } } }


	@media screen and(max-width:992px) {
		padding: r(20px) 0 r(50px) 0;
		position: relative;
		&:before {
			position: absolute;
			content: "";
			bottom: 12vh;
			left: 0;
			z-index: 12;
			width: 100vw;
			height: 20vh;
			background: -moz-linear-gradient(270deg, rgba(#ffffff,0) 0%, rgba(#000,.6) 100%);
			background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(#ffffff,0)), color-stop(100%, rgba(#000,.6)));
			background: -webkit-linear-gradient(270deg, rgba(#ffffff,0) 0%, rgba(#000,.6) 100%);
			background: -o-linear-gradient(270deg, rgba(#ffffff,0) 0%, rgba(#000,.6) 100%);
			background: -ms-linear-gradient(270deg, rgba(#ffffff,0) 0%, rgba(#000,.6) 100%);
			background: linear-gradient(180deg, rgba(#ffffff,0) 0%, rgba(#000,.6) 100%); }
		.product-wrapper {
			.product-recently-wrapper {
				.swiper-pagination-wrapper {
					margin: 1.5rem 0 1rem 0;
					display: flex; }
				.swiper-container {
					padding-bottom: 1rem;
					.swiper-wrapper {
						.swiper-slide {
							.swiper-item {
								-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								box-shadow: 0px 0px 0px 1px rgba(235,235,235,1); } } } } } } }
	@media screen and(max-width:992px) {
		&:before {
			bottom: 12vh; } }
	@media screen and(max-width:768px) {
		&:before {
			bottom: 12vh; } }
	@media screen and(max-width:576px) {
		&:before {
			bottom: 12vh; }
		.product-wrapper {
			.product-recently-wrapper {
				.swiper-container {
					.swiper-wrapper {
						.swiper-slide {
							.swiper-item {
								.swiper-image {
									height: 12.875rem; } } } } } } } }
	@media screen and(max-width:400px) {
		&:before {
			bottom: 12vh; }
		.product-wrapper {
			.product-recently-wrapper {
				.swiper-container {
					.swiper-wrapper {
						.swiper-slide {
							.swiper-item {
								.swiper-image {
									height: 10.875rem;

									10.875rem {} } } } } } } } } }
