.ripple {
	overflow: hidden; }
.ripple-effect {
	position: absolute;
	border-radius: 50%;
	width: 50px;
	height: 50px;
	background: #bbb;
	animation: ripple-animation 2s;
	@keyframes ripple-animation {
		from {
			transform: scale(1);
			opacity: 0.4; }
		to {
			transform: scale(100);
			opacity: 0; } } }
.fade-in {
	-webkit-animation: fade-in 0.7s ease-in both;
	animation: fade-in 0.7s ease-in both;
	@-webkit-keyframes fade-in {
		0% {
			opacity: 0; }

		100% {
			opacity: 1; } }


	@keyframes fade-in {
		0% {
			opacity: 0; }

		100% {
			opacity: 1; } } }
.pulsate-bck {
	-webkit-animation: pulsate-bck 0.5s ease-in-out infinite both;
	animation: pulsate-bck 0.5s ease-in-out infinite both;
	@-webkit-keyframes pulsate-bck {
		0% {
			-webkit-transform: scale(1);
			transform: scale(1); }

		50% {
			-webkit-transform: scale(0.9);
			transform: scale(0.9); }

		100% {
			-webkit-transform: scale(1);
			transform: scale(1); } }
	@keyframes pulsate-bck {
		0% {
			-webkit-transform: scale(1);
			transform: scale(1); }

		50% {
			-webkit-transform: scale(0.9);
			transform: scale(0.9); }

		100% {
			-webkit-transform: scale(1);
			transform: scale(1); } } }

.shine {
	overflow: hidden;
	position: relative;
	&:before {
		position: absolute;
		top: 0;
		left: -75%;
		z-index: 2;
		display: block;
		content: '';
		width: 50%;
		height: 100%;
		background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
		background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
		-webkit-transform: skewX(-25deg);
		transform: skewX(-25deg); }
	&:hover {
		&:before {
			-webkit-animation: shine .75s;
			animation: shine .75s;
			@-webkit-keyframes shine {
				100% {
					left: 125%; } }
			@keyframes shine {
				100% {
					left: 125%; } } } } }

