.product-filter-nav-wrapper {
	.product-filter-nav {
		padding:  0 1rem 0 2rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background: #de1f25;
		.nav-right {
			display: flex;
			justify-content: center;
			align-items: flex-start; }
		.nav-left {
			width: 100%;
			display: flex;
			justify-content: flex-start;
			align-items: center; }
		h2 {
			white-space: nowrap;
			font-size: r(22px);
			font-family: "Muli";
			color: rgb(255, 255, 255);
			font-weight: bold;
			margin: 0;
			padding: 0; }
		.price-filter {
			display: flex;
			justify-content: space-between;
			align-items: center;
			ul {
				margin: 0;
				padding: 0;
				list-style-type: none;
				display: inline-flex;
				li {
					padding: 0 2% 0 2%;
					height: 100%;
					a {
						font-size: r(22px);
						font-family: 'Muli';
						line-height: r(30px);
						white-space: nowrap;
						color: #fff;
						text-decoration: none; } } } }
		.price-check {
			ul {
				margin: 0;
				padding: 0;
				list-style-type: none;
				display: flex;
				justify-content: center;
				align-items: center;
				li {
					height: 100%;
					padding: 0 2% 0 2%;
					position: relative;
					display: inline-flex;
					input[type="checkbox"] {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						z-index: 5;
						opacity: 0;
						cursor: pointer;
						height: 100%;
						&:checked + label {
							&:before {
								content: '\f132'; } } }
					label {
						white-space: nowrap;
						color: #fff;
						margin: 0;
						padding: 0;
						font-size: r(22px);
						font-family: "Muli";
						color: rgb(255, 255, 255);
						line-height: 1.091;
						position: relative;
						display: inline-block;
						&:before {
							font-family: 'Material Design Icons';
							content: '\f131';
							font-size: 1.5rem;
							color: #fff; } } } } }
		.select-option {
			margin-left: 1rem;
			label {
				margin: 0;
				position: relative;
				&:after {
					font-family: 'Material Design Icons';
					content: '\f140';
					font-size: 1.5rem;
					color: #fff;
					right: r(-22px);
					top: 0;
					padding: 0 0 2px;
					position: absolute;
					pointer-events: none; } }
			select {
				background: #de1f25;
				-webkit-appearance: none;
				-moz-appearance: none;
				text-indent: 1px;
				text-overflow: '';
				position: relative;
				color: #fff;
				font-size: r(22px);
				padding: 0 .25rem;
				font-family: "Muli";
				border: none;
				&:focus {
					outline: none; }
				option {
					background: #fff;
					font-size: r(18px);
					font-family: "Muli";
					color: #333; }
				&::-ms-expand {
					display: none; } } }
		.select-sort {
			label {
				margin: 0;
				position: relative;
				&:after {
					font-family: 'Material Design Icons';
					content: '\f140';
					font-size: 1.5rem;
					color: #000;
					right: 0;
					top: r(-5px);
					padding: 0 0 2px;
					position: absolute;
					pointer-events: none; } }
			select {
				border-radius: 5px;
				padding: 0 1.5rem 0 .25rem;
				-webkit-appearance: none;
				-moz-appearance: none;
				text-indent: 1px;
				text-overflow: '';
				position: relative;
				option {
					font-size: r(18px);
					font-family: "Muli";
					color: rgb(0, 0, 0);
					line-height: r(26px); }
				&:focus {
					outline: none; }
				&::-ms-expand {
					display: none; } } }
		@media screen and(max-width:1700px) {
			h2 {
				font-size: r(18px); }
			.price-filter {
				ul {
					li {
						padding: 0 2% 0 1%;
						a {
							font-size: r(18px);
							line-height: r(25px); } } } }
			.price-check {
				ul {
					li {
						padding: 0 2% 0 1%;
						label {
							font-size: r(18px); } } } }
			.select-option {
				margin-left: 0.5rem;
				label {
					&:after {
						line-height: 1.8rem; } }
				select {
					font-size: r(18px); } }
			.select-sort {
				select {
					font-size: r(18px);
					line-height: r(25px); } } }
		@media screen and(max-width:1400px) {
			padding: 0 1rem;
			h2 {
				font-size: r(16px); }
			.price-filter {
				ul {
					li {
						padding: 0 2% 0 2%;
						a {
							font-size: r(16px); } } } }
			.price-check {
				ul {
					li {
						padding: 0 2% 0 1%;
						label {
							font-size: r(16px);
							&:before {
								font-size: 1rem; } } } } }
			.select-option {
				select {
					font-size: r(16px);
					option {
						font-size: r(16px); } } }
			.select-sort {
				select {
					font-size: r(14px);
					option {
						font-size: r(14px); } } } }
		@media screen and(max-width:1200px) {
			h2 {
				font-size: r(14px); }
			.price-filter {
				ul {
					li {
						padding: 0 .5% 0 1%;
						a {
							font-size: r(14px); } } } }
			.price-check {
				ul {
					li {
						padding: 0 .5% 0 1%;
						label {
							font-size: r(14px);
							&:before {
								font-size: r(14px); } } } } }
			.select-option {
				margin-left: .25rem;
				select {
					font-size: r(14px);
					option {
						font-size: r(14px); } } }
			.select-sort {
				select {
					font-size: r(14px);
					option {
						font-size: r(14px); } } } } }
	@media screen and(max-width:992px) {
		.product-filter-nav {
			.nav-left {
				margin-top: .5rem;
				flex-flow: column;
				align-items: flex-start; }
			.price-filter {
				margin-bottom: .5rem;
				ul {
					li {
						margin-right: .5rem; } } }
			.price-check {
				margin-bottom: .5rem;
				ul {
					li {
						margin-right: 1rem;
						label {
							&:before {
								margin-right: .5rem; } } } } }

			.select-option {
				margin-bottom: .5rem; }
			.select-sort {} } }
	@media screen and(max-width:768px) {
		.product-filter-nav {
			flex-flow: column;
			align-items: flex-start;
			.nav-right {
				margin-bottom: 1rem; } } }
	@media screen and(max-width:576px) {
		.product-filter-nav {
			flex-flow: column;
			align-items: flex-start;

			overflow-x: auto;
			white-space: nowrap; } } }
