@mixin nav-sub() {
	.mega-menu {
		background: #fffcf7;
		-webkit-box-shadow: 1px 2px 10px 1px rgba(0, 0, 0, 0.55);
		-moz-box-shadow: 1px 2px 10px 1px rgba(0, 0, 0, 0.55);
		box-shadow: 1px 2px 10px 1px rgba(0, 0, 0, 0.55);
		transition: .35s all ease-in-out;
		opacity: 0;
		position: absolute;
		visibility: hidden;
		top: 1rem;
		left: r(285px);
		height: auto;
		width: 100vw;
		min-height: 100%;
		text-align: left;
		z-index: 99;
		.mega-menu-list {
			background: #fffcf7;
			padding: 1.5rem 0 0  0;
			margin: 0 0;
			list-style-type: none;
			transition: .27s all ease-in-out;
			.blog-item {
				.blog-image {
					background: white;
					height: r(230px);
					width: auto;
					img {
						width: 100%;
						height: 100%;
						object-fit: scale-down; } }
				.blog-caption {
					h4 {
						font-size: r(18px);
						line-height: r(24px);
						font-family: 'Muli';
						font-weight: 600;
						margin-top: r(10px); }
					p {
						margin-top: r(10px);
						font-family: 'Muli';
						font-size: r(16px);
						line-height: r(24px); }
					.read-more {
						display: flex;
						justify-content: flex-start;
						align-items: center;

						a {
							padding: .25rem .5rem;
							background: #EE1C25;
							display: flex;
							color: #ffff;
							font-size: r(14px);
							font-family: 'Muli';
							position: relative;
							overflow: hidden;
							justify-content: center;
							align-items: center;
							margin-left: 0; } } } }
			.title-nav-sub {
				h4 {
					padding-top: 1rem;
					text-transform: uppercase;
					text-align: center;
					font-size: r(18px);
					line-height: r(24px);
					font-family: 'Muli'; } }
			.nav-sub-caption {
				p {
					text-align: center;
					font-size: r(16px);
					line-height: r(24px);
					font-family: 'Muli'; } }
			.mega-title {
				position: relative;
				color: #eee;
				margin-top: 0px;
				font-size: 14px;
				padding-left: 15px;
				padding-bottom: 13px;
				text-transform: uppercase;
				border-bottom: 1px solid #547787;
				padding-top: 15px;
				background-color: #365670;
				&:before {
					display: table;
					content: " "; }
				&:after {
					clear: both; } }
			.stander {
				list-style-type: none;
				margin: 0 0;
				padding: 0 0;
				li {
					a {
						margin-left: 0; } } } } }
	@media screen and(max-width:1600px) {
		.mega-menu {
			left: r(232px);
			top: r(4px); } }
	@media screen and(max-width:1550px) {
		.mega-menu {
			left: r(232px);
			top: r(14px); } }
	@media screen and(max-width:1500px) {
		.mega-menu {
			left: r(232px);
			top: r(14px); } }
	@media screen and(max-width:1441px) {
		.mega-menu {
			left: r(231px);
			top: r(16px);
			.mega-menu-list {
				.title-nav-sub {
					h4 {
						font-size: r(16px);
						line-height: r(24px); } }
				.nav-sub-caption {
					p {
						font-size: r(14px); } }
				.blog-item {
					.blog-image {
						height: 11.375rem; }
					.blog-caption {
						h4 {
							font-size: r(14px); }
						p {
							font-size: r(14px); }
						.read-more {
							a {
								font-size: r(14px); } } } } } } }
	@media screen and(max-width:1350px) {
		.mega-menu {
			left: r(230px);
			top: r(16px); } }
	@media screen and(max-width:1200px) {
		.mega-menu {
			left: r(193px);
			top: r(16px); } } }


