@mixin selectMixins() {
	font-family: 'Muli';
	font-size: r(15px);
	font-weight: 400;
	text-decoration: none;
	z-index: 102;
	margin-bottom: 0;
	list-style-type: none;
	padding: 0.4em 0;
	background: none;
	line-height: r(24px);
	color: #fff;
	text-align: left;
	border: none;
	cursor: pointer;
	width: 100%;
	display: flex;
	justify-content: center;
	flex-direction: row-reverse;
	position: relative;
	&:before {
		font-family: 'Material Design Icons';
		content: '\f140';
		font-size: 1rem;
		position: absolute;
		right: -1rem;
		color: #fff; }
	&.open {
		&:before {
			content: '\f143'; } }
	.default-select {
		width: 100%;
		margin: 0 0;
		padding: 0 0;
		text-indent: 1rem;
		position: relative;
		list-style-type: none;
		.default-select-options {
			span {
				font-size: r(14px);
				white-space: nowrap; } }
		.options {
			z-index: 102;
			display: block;
			list-style-type: none;
			padding: 0;
			margin: 0 0;
			background: transparent;
			position: absolute;
			width: 100%;
			max-height: 0px;
			overflow: hidden;
			border: 3px solid transparent;
			top: 1.9rem;
			left: 0;
			transition: all ease 0.25s;
			li {
				font-family: 'Muli';
				font-size: r(14px);
				z-index: 102;
				padding: 0.3rem 0;
				border-bottom: 1px solid rgba(#2F2F3F,0.4);
				&:hover {
					background:rgba(#2F2F3F,0.2) {} }
				&:last-of-type {
					border-bottom: 0; } }
			&.open {
				background: #fff;
				max-height: 1000%;
				z-index: 102;
				width: 100%;
				color: #333;
				border: none;
				border-top: 3px solid transparent; } } }
	@media screen and(max-width:1600px) {
		font-size: r(13px);
		.default-select {
			.default-select-options {
				span {
					font-size: r(11px); } }
			.options {

				&.open {
					width: 115%;
					border: 1px solid rgba(#2F2F3F,0.4); }
				li {
					font-size: r(11px);
					white-space: nowrap; } } } }
	@media screen and(max-width:1250px) {
		margin-left: -0.8rem; }

	@media screen and(max-width:1250px) {
		font-size: r(12px); } }
