.product-top-banner {
	background: url('../images/options-1/bg-linear-gradient-gold.png');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	-webkit-box-shadow: inset 0px 5px 0px 0px #684002;
	-moz-box-shadow: inset 0px 5px 0px 0px #684002;
	box-shadow: inset 0px 5px 0px 0px #684002;
	.product-top-banner-wrapper {
		padding: r(25px) 0;
		display: flex;
		justify-content: center;
		align-items: center;
		img {
			max-width: 100%;
			height: auto;
			width: 99%; } }
	@media screen and(max-width:768px) {
		.product-top-banner-wrapper {
			flex-flow: column;
			.mb {
				margin-bottom: 1rem; } } } }
