.product-display {
	padding: 50px 0 0 0;
	.watch-more {
		padding-bottom: .5rem;
		border-bottom: 1px solid #ed1c24;
		display: flex;
		justify-content: center;
		align-items: center;
		a {
			text-align: center;
			text-decoration: none;
			font-size: r(20px);
			line-height: r(27px);
			font-family: "Muli";
			color: rgb(237, 28, 36);
			font-weight: bold;
			&:hover {
				text-decoration: underline; } } }
	.product-title {
		display: flex;
		justify-content: center;
		align-items: center;
		h2 {
			text-align: center;
			margin-bottom: 2rem;
			font-size: r(36px);
			color: #EC1F26;
			font-family: 'Muli';
			line-height: r(44px);
			font-weight: 900; } }
	@media screen and(max-width:1600px) {
		.product-title {
			h2 {
				font-size: r(28px);
				line-height: r(34px); } } }
	@media screen and(max-width:992px) {
		.watch-more {
			a {
				font-size: r(18px);
				line-height: r(25px); } }
		.product-title {
			h2 {
				font-size: r(24px);
				line-height: r(32px); } } }
	@media screen and(max-width:576px) {
		.product-title {
			h2 {
				font-size: r(20px);
				line-height: r(28px); } } }
	.figure {
		position: relative;
		margin-bottom: 2rem;
		width: 98%;
		&:hover {
			-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
			-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
			box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
			.figure-image {
				.add-cart {
					display: flex;
					opacity: 1; } }
			.figure-caption {
				background: #fafafa; } }
		.hot-item {
			position: absolute;
			top: .005rem;
			left: 0;
			background: #ed1c24;
			z-index: 11;
			h5 {
				margin: 0 0;
				padding: .1rem .75rem;
				font-size: r(16px);
				font-family: 'Muli';
				color: rgb(247, 247, 247);
				font-weight: bold; } }
		@include figure-caption-product;
		.figure-image {
			position: relative;
			height: 19.675rem;
			width: auto;
			img {
				width: 100%;
				height: 100%;
				object-fit: scale-down; }
			.add-cart {
				display: none;
				opacity: 0;
				position: relative;
				margin-top: -3rem;
				justify-content: center;
				align-items: center;
				ul {
					padding: .25rem .5rem;
					background: url('../images/options-1/bg-linear-gradient-gold-small2.png');
					background-repeat: no-repeat;
					background-size: cover;
					background-position: center;
					display: flex;
					justify-content: center;
					align-items: center;
					list-style-type: none;
					li {
						&:hover {
							a {
								&:after {
									opacity: 1; } }
							.label {
								opacity: 1; } }
						a {
							text-decoration: none;
							position: relative;
							&:after {
								top: -.6rem;
								left: 50%;
								border: solid transparent;
								content: " ";
								height: 0;
								opacity: 0;
								width: 0;
								position: absolute;
								pointer-events: none;
								border-color: rgba(237, 28, 36, 0);
								border-top-color: #ed1c24;
								border-width: 7px;
								margin-left: -7px; }
							i {
								color: #ed1c24;
								margin: 0 .75rem; } }
						.label {
							left: 50%;
							opacity: 0;
							transform: translateX(-50%);
							position: absolute;
							top: -2.5rem;
							padding: .25rem .5rem;
							background: #ed1c24;
							display: flex;
							justify-content: center;
							align-items: center;
							p {
								color: #fff;
								margin-bottom: 0;
								font-size: r(15px);
								font-family: 'Muli';
								color: rgb(255, 255, 255); } } } }
				@media screen and (max-width:521px) {
					ul {
						li {
							.label {
								p {
									font-size: r(13px);
									line-height: r(21px);
									white-space: nowrap; } } } } } } } }
	@media screen and(max-width:1919px) {
		.figure {
			.figure-image {
				height: r(270px); } } }
	@media screen and(max-width:1200px) {
		padding: 30px 0;
		.figure {
			.figure-image {
				padding: 0 1rem;
				height: 14.875rem; } } }
	@media screen and(max-width:992px) {
		.figure {
			margin-bottom: 1rem;
			-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
			-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
			box-shadow: 0px 0px 0px 1px rgba(235,235,235,1); } }
	@media screen and(max-width:767.9px) {
		padding: 20px 0;
		.figure {
			margin: 0 0 1rem 0; } }
	@media screen and(max-width:576px) {
		.figure {
			.figure-image {
				padding: .5rem .5rem;
				height: 11rem; } } }
	@media screen and(max-width:400px) {
		.figure {
			.figure-image {
				height: 9rem; } } } }
