.product-media-iframe {
	.product-media-wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 -15px 3rem -15px;
		.row {
			width: 100%; }
		@media screen and(max-width:1200px) {
			margin-bottom: 2rem; }
		@media screen and(max-width:1200px) {} }

	.media-wrapper {
		position: relative;
		display: block;
		width: 100%;
		padding: 0;
		overflow: hidden;
		padding-top: 58.257143%;
		&:before {
			display: block;
			content: ""; }
		iframe {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border: 0; }
		@media screen and(max-width:768px) {
			margin-bottom: 1rem; } }
	.media-title-product {
		margin-bottom: 46px;
		h2 {
			font-size: r(20px);
			font-family: "Muli";
			margin-top: 1rem;
			color: rgb(51, 51, 51);
			font-weight: bold;
			text-transform: uppercase; }
		span {
			font-size: r(17px);
			line-height: r(25px);
			font-family: "Muli";
			color: rgb(102, 102, 102);
			line-height: 1.412;
			a {
				color: #0096ff;
				font-size: r(17px);
				text-decoration: underline;
				line-height: r(25px); } }
		@media screen and(max-width:1200px) {
			margin-bottom: 30px;
			h2 {
				font-size: r(18px); }
			span {
				font-size: r(16px);
				line-height: r(23px);
				a {
					font-size: r(16px);
					line-height: r(23px); } } } }
	.media-banner {
		img {
			max-width: 100%;
			height: auto;
			width: 100%; } }
	.media-title {
		h2 {
			font-size: r(24px);
			font-family: "Muli";
			color: rgb(51, 51, 51);
			font-weight: bold;
			line-height: r(32px);
			margin: 1rem 0 3.5rem 0; }
		@media screen and(max-width:1600px) {
			h2 {
				font-size: r(20px);
				line-height: r(26px); } }
		@media screen and(max-width:1200px) {
			h2 {
				font-size: r(18px);
				line-height: r(26px);
				margin: 1rem 0 2rem 0; } }
		@media screen and(max-width:768px) {
			h2 {
				margin: 1rem 0 1.5rem 0; } } }
	.other-video-title {
		h2 {
			font-size: r(30px);
			font-family: "Muli";
			color: rgb(222, 31, 37);
			text-transform: uppercase; }
		@media screen and(max-width:1600px) {
			h2 {
				font-size: r(24px);
				line-height: r(32px); } }
		@media screen and(max-width:1200px) {
			h2 {
				font-size: r(22px);
				line-height: r(30px); } }
		@media screen and(max-width:1200px) {
			h2 {
				font-size: r(18px);
				line-height: r(25px); } } }
	.product-media-thumbnail-wrapper {
		margin: 0 -15px;
		display: flex;
		flex-flow: row wrap;
		position: relative;

		.item-w20 {
			padding: 0 15px;
			flex: 0 1 20%; }
		.figure {
			margin-bottom: 3rem;
			.figure-image {
				a {
					text-decoration: none; }
				img {
					margin-bottom: 1rem;
					max-width: 100%;
					height: auto;
					width: 100%; } }
			.figure-caption {
				h5 {
					font-size: r(20px);
					font-family: "Muli";
					color: rgb(51, 51, 51);
					font-weight: bold;
					line-height: r(28px); }
				span {
					font-size: r(17px);
					font-family: "Muli";
					color: rgb(102, 102, 102);
					line-height: r(24px);
					a {
						color: #0096ff;
						font-size: r(17px);
						text-decoration: underline !important;
						line-height: r(25px); } } } }
		@media screen and(max-width:1600px) {
			.item-w20 {
				flex: 0 1 25%; }
			.figure {
				.figure-caption {
					h5 {
						font-size: r(18px);
						line-height: r(25px); }
					span {
						font-size: r(16px);
						line-height: r(16px);
						a {
							font-size: r(16px);
							line-height: r(16px); } } } } }
		@media screen and(max-width:1400px) {
			.item-w20 {
				flex: 0 1 33.333%; }
			.figure {
				.figure-caption {
					h5 {
						font-size: r(16px);
						line-height: r(23px); }
					span {
						font-size: r(14px);
						line-height: r(21px);
						a {
							ffont-size: r(14px);
							line-height: r(21px); } } } } }
		@media screen and(max-width:576px) {
			.item-w20 {
				flex: 0 1 50%; } } } }

