.news-article {
	margin-bottom: 50px;
	.figure-image {
		margin-bottom: 1rem;
		img {
			max-width: 100%;
			height: auto;
			width: 100%; } }
	.figure-caption {
		&:hover {
			h4 {
				transition: .3s all ease-in-out;
				color: #b4b4b4; } }
		a {
			text-decoration: none; }
		h4 {
			font-size: r(22px);
			font-family: "Muli";
			color: rgb(51, 51, 51);
			font-weight: bold;
			line-height: r(25px); }
		h5 {
			font-size: r(18px);
			font-family: "Muli";
			color: rgb(237, 28, 36);
			line-height: r(25px); }
		p {
			font-size: r(20px);
			font-family: "Muli";
			color: rgb(102, 102, 102);
			line-height: r(26px); } }
	@media screen and(max-width:1600px) {
		.figure-caption {
			h4 {
				font-size: r(20px);
				line-height: r(25px); }
			p {
				font-size: r(18px);
				line-height: r(26px); } } }
	@media screen and(max-width:1200px) {
		margin-bottom: 30px;
		.figure-caption {
			h4 {
				font-size: r(18px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				font-weight: bold;
				line-height: r(24px); }
			h5 {
				font-size: r(16px);
				font-family: "Muli";
				color: rgb(237, 28, 36);
				line-height: r(24px); }
			p {
				font-size: r(16px);
				font-family: "Muli";
				color: rgb(102, 102, 102);
				line-height: r(24px); } } }

	@media screen and(max-width:768px) {
		margin-bottom: 20px; } }
