.product-discount {
	overflow: hidden;
	padding: 1.25rem 0;
	.product-wrapper {
		border-bottom: 1px solid #EC1D25;
		.product-tab-nav {
			display: flex;
			justify-content: center;
			align-items: centers;
			ul {
				display: flex;
				list-style-type: none;
				overflow: hidden;
				width: 100%;
				justify-content: center;
				padding: 0.8rem 0 r(8px) 0;
				margin-bottom: 0;
				li {
					&:not(:last-child) {
						margin-right: 1rem; }
					&.active {
						a {
							color: #533d0d;
							background: url('../images/options-1/bg-linear-gradient-gold-small.png');
							background-size: cover;
							background-position: center;
							background-repeat: no-repeat;
							-webkit-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							-moz-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							box-shadow: 3px 3px 0px 0px rgba(111,50,5,1); } }
					a {
						display: flex;
						justify-content: center;
						align-items: center;
						padding: .65rem 2rem;
						text-decoration: none;
						color: #fff;
						background: #EC1D25;
						font-family: 'Muli';
						font-size: r(18px);
						line-height: r(24px);
						font-weight: 900; } } } }
		@media screen and(max-width:1600px) {
			.product-tab-nav {
				ul {
					li {
						a {
							font-size: r(16px); } } } } }
		@media screen and(max-width:1200px) {
			.product-tab-nav {
				ul {
					li {
						a {
							font-size: r(14px);
							line-height: r(22px);
							padding: .5rem .5rem; } } } } }
		@media screen and(max-width:992px) {
			.product-tab-nav {
				ul {
					li {
						&:not(:last-child) {
							margin-right: .5rem; }
						a {
							font-size: r(13px);
							line-height: r(21px);
							padding: .5rem .35rem;
							font-weight: 700;
							white-space: nowrap; } } } } }
		@media screen and(max-width:768px) {
			.product-tab-nav {
				ul {
					li {
						&:nth-child(n+3) {
							display: none; }
						a {
							font-size: r(16px);
							line-height: r(24px);
							padding: .5rem .75rem; } } } } }
		@media screen and(max-width:576px) {
			.product-tab-nav {
				ul {
					li {
						&:not(:last-child) {
							margin-right: .35rem; }
						a {
							font-size: r(13px);
							line-height: r(21px);
							padding: .5rem .45rem; } } } } }
		@media screen and(max-width:350px) {
			.product-tab-nav {
				ul {
					li {
						a {
							font-size: r(12px);
							line-height: r(20px); } } } } }
		.product-figure-wrapper {
			h2 {
				text-align: center;
				font-size: r(36px);
				color: #EC1F26;
				font-family: 'Roboto';
				line-height: r(44px); }
			.tab-content {
				.tab-panel {
					width: 100%;
					height: 0;
					opacity: 0;
					&.active {
						height: 100%;
						opacity: 1; } } }
			.swiper-container {
				padding: 1rem 0 0 0;
				.swiper-wrapper {
					.swiper-slide {
						&:hover {
							.figure {
								-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								.figure-image {
									.add-cart {
										opacity: 1; } } } }
						.figure {
							width: 98%;
							position: relative;
							&:hover {
								.figure-caption {
									background: #fafafa; } }
							.hot-item {
									position: absolute;
									top: 0;
									left: 0;
									background: #ed1c24;
									z-index: 11;
									h5 {
										margin: 0 0;
										padding: .1rem .75rem;
										font-size: r(16px);
										font-family: 'Muli';
										color: rgb(247, 247, 247);
										font-weight: bold; } }
							@include figure-caption-product;
							.figure-image {
								position: relative;
								height: r(270px);
								width: auto;
								img {
									width: 100%;
									height: 100%;
									object-fit: scale-down; }
								.add-cart {
									opacity: 0;
									position: relative;
									margin-top: -3rem;
									display: flex;
									justify-content: center;
									align-items: center;
									ul {
										padding: .25rem .5rem;
										background: url('../images/options-1/bg-linear-gradient-gold-small2.png');
										background-repeat: no-repeat;
										background-size: cover;
										background-position: center;
										display: flex;
										justify-content: center;
										align-items: center;
										list-style-type: none;
										li {
											&:hover {
												a {
													&:after {
														opacity: 1; } }
												.label {
													opacity: 1; } }
											a {
												text-decoration: none;
												position: relative;
												&:after {
													top: -.6rem;
													left: 50%;
													border: solid transparent;
													content: " ";
													height: 0;
													opacity: 0;
													width: 0;
													position: absolute;
													pointer-events: none;
													border-color: rgba(237, 28, 36, 0);
													border-top-color: #ed1c24;
													border-width: 7px;
													margin-left: -7px; }
												i {
													color: #ed1c24;
													margin: 0 .75rem; } }
											.label {
												left: 50%;
												opacity: 0;
												transform: translateX(-50%);
												position: absolute;
												top: -2.5rem;
												padding: .25rem .5rem;
												background: #ed1c24;
												display: flex;
												justify-content: center;
												align-items: center;
												p {
													color: #fff;
													width: {}
													margin-bottom: 0;
													font-size: r(15px);
													font-family: 'Muli';
													color: rgb(255, 255, 255); } } } } } } } } } }
			.swiper-pagination-wrapper {
				margin: 0 0 1rem 0;
				display: none;
				justify-content: center;
				align-items: center;
				.swiper-pagination {
					.swiper-pagination-bullet {
						background: #EC1F26;
						opacity: 1; }
					.swiper-pagination-bullet-active {
						background: -moz-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -webkit-gradient(linear, left bottom, right top, color-stop(0%, rgba(255,0,0,1)), color-stop(50%, rgba(255,255,0,1)), color-stop(100%, rgba(255,0,0,1)));
						background: -webkit-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -o-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: -ms-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						background: linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
						filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff0000', endColorstr='#FF0000',GradientType=1 ); }
					span {
						&:not(:last-child) {
							margin-right: .425rem; } } } } }
		.view-more {

			display: flex;
			justify-content: center;
			align-items: center;
			padding: r(10px);
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				color: #EC1F26;
				font-size: r(18px);
				font-family: 'Roboto';
				line-height: r(24px);
				font-weight: 700; } } }
	@media screen and(max-width:1919px) {
		.product-wrapper {
			.product-figure-wrapper {
				h2 {
					font-size: r(32px);
					line-height: r(40px); } } } }
	@media screen and(max-width:1600px) {
		.product-wrapper {
			.product-figure-wrapper {
				h2 {
					font-size: r(28px);
					line-height: r(36px); }
				.swiper-container {
					.swiper-wrapper {
						margin-left: 1px; } } } } }
	@media screen and(max-width:1200px) {
		.product-wrapper {
			.product-figure-wrapper {
				.swiper-container {
					.swiper-wrapper {
						.swiper-slide {
							.figure {
								.figure-image {
									padding: 0 1rem;
									height: 14.875rem; } } } } } } } }

	@media screen and(max-width:992px) {
		.product-wrapper {
			.product-figure-wrapper {
				.swiper-pagination-wrapper {
					display: flex;
					margin-top: 1rem; }
				h2 {
					font-size: r(24px);
					line-height: r(32px); }
				.swiper-container {
					.swiper-wrapper {
						margin-left: 1px;
						.swiper-slide {
							display: flex;
							height: auto;
							align-items: center;
							justify-content: center;
							margin-bottom: 0;
							align-self: stretch;
							.figure {
								-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								box-shadow: 0px 0px 0px 1px rgba(235,235,235,1); } } } } } } }

	@media screen and(max-width:768px) {
		.product-wrapper {
			.product-figure-wrapper {
				.swiper-pagination-wrapper {
					display: flex; }
				.swiper-container {
						.swiper-wrapper {
							margin-left: 1px;
							.swiper-slide {
								.figure {
									-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
									-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
									box-shadow: 0px 0px 0px 1px rgba(235,235,235,1); } } } } } } }
	@media screen and(max-width:576px) {
		.product-wrapper {
			.product-figure-wrapper {
				.swiper-pagination-wrapper {
					display: flex; }
				h2 {
					font-size: r(20px);
					line-height: r(28px); }
				.swiper-container {
					.swiper-wrapper {
						margin-left: 1px;

						.swiper-slide {
							.figure {
								-webkit-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								-moz-box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								box-shadow: 0px 0px 0px 1px rgba(235,235,235,1);
								max-width: 100%;
								.figure-image {
									padding: .5rem .5rem;
									height: 11rem; } } } } } } } }

	@media screen and(max-width:400px) {
		.product-wrapper {
			.product-figure-wrapper {
				.swiper-container {
					.swiper-wrapper {
						.swiper-slide {
							.figure {
								.figure-image {
									height: 10rem; } } } } } } } } }
