.social-network-media-player {
	.social-media-player-wrapper {
		background: #f7f7f7;
		padding: r(50px) r(20px) 0 r(50px);
		.line {
			&:first-child {
				border-right: 30px solid rgba(#000,0); } }
		.social-media-content {
			position: relative;
			display: block;
			width: 100%;
			padding: 0;
			overflow: hidden;
			padding-top: 58.257143%;
			&:before {
				display: block;
				content: ""; }
			iframe {
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border: 0; } }
		.social-media-playlist {
			.media-thumbnail {
				.media-thumbnail-wrapper {
					margin-bottom: r(35px);
					.thumbnail-iframe {
						position: relative;
						display: block;
						width: 100%;
						padding: 0;
						overflow: hidden;
						padding-top: 73.257143%;
						&:before {
							display: block;
							content: ""; }
						iframe {
							position: absolute;
							top: 0;
							bottom: 0;
							left: 0;
							width: 100%;
							height: 100%;
							border: 0; } }
					.media-caption {
						h4 {
							font-size: r(20px);
							line-height: r(26px);
							font-family: 'Muli';
							color: rgb(51, 51, 51);
							font-weight: bold;
							line-height: 1.2; }
						h5 {
							font-size: r(17px);
							font-family: 'Muli';
							color: #666666;
							line-height: 1.2;
							font-weight: 400; }
						p {
							font-size: r(17px);
							font-family: 'Muli';
							color: #666666;
							line-height: 1.2;
							font-weight: 400; } } } }
			@media screen and(max-width:1700px) {
				.media-thumbnail {
					.media-thumbnail-wrapper {
						margin-bottom: r(10px); } } }
			@media screen and(max-width:1600px) {
				.media-thumbnail {
					.media-thumbnail-wrapper {
						margin-bottom: r(20px);
						.thumbnail-iframe {
							padding-top: 76.25%; }
						.media-caption {
							h4 {
								font-size: r(15px);
								margin-bottom: .25rem; }
							h5 {
								font-size: r(15px);
								margin-bottom: .25rem; }
							p {
								margin-bottom: .89rem;
								&:last-child {
									margin-bottom: 0; } } } } } }
			@media screen and(max-width:1300px) {
				.media-thumbnail {
					.media-thumbnail-wrapper {
						margin-bottom: 0;
						.media-caption {
							h4 {
								font-size: r(14px);
								margin-bottom: .5rem; }
							h5 {
								font-size: r(14px);
								margin-bottom: .5rem; }
							p {
								margin-bottom: .89rem;
								font-size: r(14px);
								&:last-child {
									margin-bottom: .89rem; } } } } } }
			@media screen and(max-width:1200px) {
				.media-thumbnail {
					.media-thumbnail-wrapper {
						margin-bottom: 1rem;
						.media-caption {
							h4 {
								font-size: r(20px);
								line-height: r(26px);
								font-family: 'Muli';
								color: rgb(51, 51, 51);
								font-weight: bold;
								line-height: 1.2; }
							h5 {
								font-size: r(17px);
								font-family: 'Muli';
								color: #666666;
								line-height: 1.2;
								font-weight: 400; }
							p {
								font-size: r(17px);
								font-family: 'Muli';
								color: #666666;
								line-height: 1.2;
								font-weight: 400; } }
						.thumbnail-iframe {
							padding-top: 56.25%; } } } } } }

	@media screen and(max-width:1800px) {
		.social-media-player-wrapper {
			.social-media-content {
				padding-top: 64.257143%; } } }
	@media screen and(max-width:1700px) {
		.social-media-player-wrapper {
			.social-media-content {
				padding-top: 60.257143%; } } }
	@media screen and(max-width:1600px) {
		.social-media-player-wrapper {
			padding: 3.125rem 3.25rem 1.125rem 3.125rem;

			.social-media-content {
				padding-top: 55.257143%;
				.media-caption {
					h4 {
						font-size: r(16px);
						font-family: 'Muli';
						color: rgb(51, 51, 51);
						font-weight: bold;
						line-height: 1.2; }
					h5 {
						font-size: r(18px);
						line-height: r(26px);
						font-family: 'Muli';
						color: #666666;
						line-height: 1.2;
						font-weight: 400; }
					p {
						font-size: r(18px);
						line-height: r(26px);
						font-family: 'Muli';
						color: #666666;
						line-height: 1.2;
						font-weight: 400; } } } } }
	@media screen and(max-width:1500px) {
		.social-media-player-wrapper {
			.social-media-content {
				padding-top: 57.257143%; } } }
	@media screen and(max-width:1367px) {
		.social-media-player-wrapper {
			padding: 3.125rem 3.25rem 3.125rem 3.125rem;
			.social-media-content {
				padding-top: 69.257143%; } } }
	@media screen and(max-width:1300px) {
		.social-media-player-wrapper {
			padding: 3.125rem 3.25rem 1.125rem 3.125rem;
			.social-media-content {
				padding-top: 69.257143%; } } }
	@media screen and(max-width:1200px) {
		.social-media-player-wrapper {
			.social-media-content {
				margin-bottom: 1rem;
				padding-top: 69.257143%; } } }
	@media screen and(max-width:992px) {
		display: none;
		.social-media-player-wrapper {
			background: none; } } }
