.article-title {
	padding: r(30px) 0;
	background: url('../images/options-1/title-bg.png');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	-webkit-box-shadow: inset 0px 5px 0px 0px #684002;
	-moz-box-shadow: inset 0px 5px 0px 0px #684002;
	box-shadow: inset 0px 5px 0px 0px #684002;
	h2 {
		text-align: center;
		font-size: r(32px);
		font-weight: 900;
		font-family: 'Muli';
		color: rgb(237, 28, 36);
		text-transform: uppercase;
		white-space: nowrap;
		line-height: r(40px); }
	.find-showroom {
		display: flex;
		justify-content: center;
		align-items: center;
		input[type="submit"] {
			padding: .5rem 1rem;
			border: none;
			text-transform: uppercase;
			font-family: 'Muli';
			font-size: r(20px);
			font-weight: 700;
			color: #fff;
			background: rgb(237, 28, 36); } }

	@media screen and(max-width:1600px) {
		h2 {
			font-size: r(28px);
			line-height: (35px); } }
	@media screen and(max-width:1200px) {
		h2 {
			font-size: r(22px);
			line-height: (30px); } }
	@media screen and(max-width:992px) {
		margin-top: 3rem; }
	@media screen and(max-width:768px) {
		h2 {
			font-size: r(20px);
			line-height: (27px); } }
	@media screen and(max-width:400px) {
		h2 {
			font-size: r(18px);
			line-height: (25px); } } }
