.showroom-wrapper {
	margin-bottom: 50px;
	.tab-panel {
		display: none;
		&.active {
			display: block; } }
	.top-nav-showroom {
		padding-bottom: 50px;
		ul {
			list-style-type: none;
			margin: 0;
			padding: 0;
			li {
				margin-right: .5rem;
				display: inline;
				&.active {
					a {
						position: relative;
						color: #533d0d;
						background: url('../images/options-1/bg-linear-gradient-gold-small.png');
						background-size: cover;
						background-position: center;
						background-repeat: no-repeat;
						-webkit-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
						-moz-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
						box-shadow: 3px 3px 0px 0px rgba(111,50,5,1); } }
				a {
					padding: .5rem 1rem;
					background: rgb(237, 28, 36);
					display: inline-flex;
					justify-content: center;
					align-items: center;
					text-transform: uppercase;
					font-size: r(22px);
					font-family: "Muli";
					color: rgb(255, 255, 255);
					font-weight: bold;
					line-height: r(30px);
					text-decoration: none; } } } }
	.figure {
		margin-bottom: 1rem;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		.figure-image {
			margin-right: 1.5rem;
			img {
				max-width: 100%;
				height: auto;
				width: 100%; } }
		.figure-caption {
			h5 {
				font-size: r(20px);
				font-family: "Muli";
				white-space: nowrap;
				color: rgb(51, 51, 51);
				font-weight: bold;
				line-height: r(27px); }
			p {
				margin-bottom: .5rem;
				font-size: r(20px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				font-weight: 400;
				line-height: r(27px);
				line-height: 1.2; }
			.figure-fancy {
				p {
					font-size: r(18px);
					font-family: "Muli";
					color: rgb(237, 28, 36);
					font-weight: bold;
					line-height: 1.2;
					line-height: r(24px); }
				a {
					&:not(:first-child) {
						img {
							margin-left: .5rem; } } } } } }
	@media screen and(max-width:1600px) {
		.figure {
			.figure-caption {
				h5 {
					font-size: r(16px);
					line-height: r(23px); }
				p {
					font-size: r(16px);
					line-height: r(23px); }
				.figure-fancy {
					p {
						font-size: r(14px);
						line-height: r(22px); }
					a {
						&:not(:first-child) {
							img {
								margin-left: .25rem; } } } } } } }
	@media screen and(max-width:1400px) {
		.figure {
			.figure-caption {
				.figure-fancy {
					a {
						img {
							max-width: 100%;
							height: auto;
							width: 18%; } } } } } }
	@media screen and(max-width:992px) {
		margin-bottom: 30px;
		.top-nav-showroom {
			padding-bottom: 30px; } }
	@media screen and(max-width:768px) {
		.top-nav-showroom {
			overflow: visible;
			ul {
				overflow-x: auto;
				white-space: nowrap;

				li {
					margin-bottom: .5rem;
					a {
						font-size: r(14px);
						line-height: 22px; } } } }
		.figure {
			padding: 1rem 0;
			flex-flow: column;
			.figure-image {
				margin: 0;
				img {
					margin-bottom: 1rem; } } } }
	@media screen and(max-width:576px) {
		margin-bottom: 20px;
		.top-nav-showroom {
			padding-bottom: 20px; } } }

