.header-mobile {
	background: #EC1F26;
	height: 3rem;
	position: fixed;
	z-index: 800;
	width: 100vw;
	.mobile-header-wrapper {
		padding-left: 0;
		-webkit-transition: all 0.5s ease;
		-moz-transition: all 0.5s ease;
		-o-transition: all 0.5s ease;
		transition: all 0.5s ease;
		&.active {
			.sidebar-wrapper {
				margin-left: 0; } }
		.sidebar-wrapper {
			top: 3rem;
			z-index: 600;
			position: fixed;
			left: 0;
			width: r(320px);
			height: 100%;
			margin-left: r(-320px);
			overflow-y: auto;
			background: #fffcf7;
			-webkit-transition: all 0.5s ease;
			-moz-transition: all 0.5s ease;
			-o-transition: all 0.5s ease;
			transition: all 0.5s ease;
			.sidebar-menu {
				list-style-type: none;
				margin: 0;
				padding: 0;
				li {
					border-bottom: 1px solid rgba(#6f6f6f,.2);
					&.active {
						&.have-children {
							&:after {
								-moz-transform: rotate(90deg);
								-o-transform: rotate(90deg);
								-webkit-transform: rotate(90deg);
								transform: rotate(90deg); } } }
					&.have-children {

						position: relative;
						&:after {
							right: r(35px);
							top: 0.5rem;
							font-family: 'Material Design Icons';
							content: '\f142';
							font-size: 2rem;
							color: #333333;
							position: absolute;
							pointer-events: none;
							transition: all 0.2s ease; } }
					&:hover {
						background: none;
						a {
							background: rgba(#b6bdc7,.5); } }
					a {
						position: relative;
						display: block;
						color: #333333;
						text-decoration: none;
						padding: r(20px) r(25px);
						font-size: r(15px);
						font-family: 'Muli';
						&:hover {
							background: #b6bdc7; }
						&::first-letter {
							text-transform: uppercase; } }
					ul {
						list-style-type: none;
						display: none;
						margin: 0;
						padding: 0;
						li {
							border-bottom: none; } } } } }

		.mobile-header-content-wrapper {
			display: flex;
			justify-content: space-between;
			align-items: center;
			.nav-mobile-left {
				display: flex;
				justify-content: center;
				align-items: center;
				.hamburger-button {
					a {
						margin-top: .5rem;
						text-decoration: none;
						p {
							margin-top: -0.5rem;
							color: #fff;
							font-size: r(13px);
							font-family: 'Muli';
							text-decoration: none; } } }
				.form-group-mobile-search {
					margin: -1rem 0 0 1rem;
					.search-input {
						.mt-search {
							position: relative;
							display: flex;
							justify-content: center;
							align-items: center;
							width: 3rem;
							height: 3rem;
							.search {
								position: relative;
								width: 3rem;
								height: 3rem;
								background: #EC1F26;
								border-radius: 50%;
								transition: all .3s;
								z-index: 400;
								box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
								&:hover {
									cursor: pointer; }
								&:before {
									content: "";
									position: absolute;
									margin: auto;
									top: 12px;
									right: -13px;
									bottom: -6px;
									left: 6px;
									width: 15px;
									height: 2px;
									background: white;
									transform: rotate(45deg);
									transition: all .3s; }
								&:after {
									content: "";
									position: absolute;
									margin: auto;
									top: -7px;
									right: -3px;
									bottom: -3px;
									left: -5px;
									width: r(20px);
									height: r(20px);
									border-radius: 50%;
									border: 2px solid white;
									transition: all .3s; } }
							input {
								font-family: 'Muli';
								position: absolute;
								top: 0;
								font-size: r(14px);
								font-weight: 400;
								left: 0;
								width: 3rem;
								height: 3rem;
								outline: none;
								border-radius: 0 0;
								background: #EC1F26;
								color: white;
								text-shadow: none;
								transition: all ease-in-out .3s;
								opacity: 0;
								z-index: 700;
								font-weight: bolder;
								letter-spacing: 0.1rem;
								&:hover {
									cursor: pointer; }
								&:focus {
									top: 3rem;
									padding-left: r(20px);
									opacity: 1;
									left: 0;
									background: white;
									color: #333;
									font-family: 'Muli';
									font-size: r(14px);
									font-weight: 400;
									line-height: r(24px);
									cursor: text;
									width: 100vw;
									min-width: r(320px); }
								&:focus ~ .search {
									background: rgba(#151515,.5);
									z-index: 600;
									font-size: r(14px);
									font-family: 'Muli';
									font-weight: 400;
									&:before {
										top: -6px;
										left: 0;
										right: 0;
										width: 20px; }
									&:after {
										top: -4px;
										left: -3px;
										width: 20px;
										height: 2px;
										border: none;
										background: white;
										border-radius: 0%;
										transform: rotate(-45deg); } }
								&:placeholder {
									color: white;
									opacity: 0.5;
									font-size: r(14px);
									font-family: 'Muli';
									font-weight: 400;
									font-size: 1.5rem;
									font-weight: bolder; } } } } } }
			.nav-brand {
				margin-top: -1rem;
				img {} }
			.nav-mobile-right {
				margin-top: -1rem;
				display: flex;
				justify-content: center;
				align-items: center;
				.hot-line-mobile {
					p {
						text-align: right;
						font-size: r(13px);
						font-family: 'Muli';
						color: #fff;
						margin-bottom: 0; }
					h5 {
						text-align: right;
						color: #fff;
						font-size: r(13px);
						font-family: 'Muli'; } }
				.mobile-hotline {
					a {
						text-decoration: none;
						img {
							width: 80%; } } }
				@media screen and(min-width:350px) {
					.hot-line-mobile {
						margin-right: .5rem; } } } } }

	@media screen and(min-width:992.1px) {
		display: none; } }
