.breadcrumb-wrapper {
	position: relative;
	padding: .35rem 0;
	margin-bottom: r(50px);
	@media screen and(max-width:1600px) {
		margin-bottom: r(40px); }
	@media screen and(max-width:1200px) {
		margin-bottom: r(30px); }
	@media screen and(max-width:768px) {
		margin-bottom: r(20px); }
	&:after {
		content: '';
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100vw;
		height: 1px;
		background: #f5f5f5; }
	ol {
		padding: 0;
		margin: 0;
		list-style-type: none;
		li {
			display: inline-block;
			font-size: r(18px);
			font-family: 'Muli';
			color: rgb(102, 102, 102);
			line-height: r(25px);
			&:not(.active) {
				a {
					position: relative;
					&:after {
						font-family: 'Material Design Icons';
						content: '\f142';
						font-size: 1rem;
						bottom: 0;
						color: rgb(102, 102, 102); } } }

			a {
				font-size: r(18px);
				font-family: 'Muli';
				color: rgb(102, 102, 102);
				line-height: r(25px);
				text-decoration: none; } }
		@media screen and(max-width:992px) {
			li {
				font-size: r(14px);
				a {
					font-size: r(14px); } } } } }
