.verification-award {
	.figure {
		margin-bottom: 2rem;
		.figure-image {
			img {
				max-width: 100%;
				height: auto;
				width: 100%;
				margin-bottom: 1rem; } }
		.figure-caption {
			h5 {

				font-size: r(22px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				font-weight: bold;
				line-height: r(30px);
				margin-bottom: .5rem; }
			p {
				font-size: r(20px);
				font-family: "Muli";
				color: rgb(51, 51, 51);
				font-weight: 400;
				line-height: r(30px);
				margin-bottom: .5rem; } } }
	@media screen and(max-width:1600px) {
		.figure {
			.figure-caption {
				h5 {
					font-size: r(18px);
					line-height: r(26px); }
				p {
					font-size: r(16px);
					line-height: r(24px); } } } } }

