.social-network-comment {
	.media-social-network {
		border-top: 1px solid #e8e8e8;
		padding: 1rem  0;
		border-bottom: 1px solid #e8e8e8;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.social-article {
			display: flex;
			justify-content: center;
			align-items: center;
			.mr {
				margin-right: 2rem; }
			a {
				display: flex;
				justify-content: flex-start;
				align-items: center;
				text-decoration: none;
				em {
					color: rgb(51, 51, 51);
					margin-right: .5rem;
					font-size: r(20px); }
				p {
					margin: 0;
					font-size: r(20px);
					font-family: "Muli";
					color: rgb(51, 51, 51);
					font-weight: bold;
					line-height: r(28px); } }
			@media screen and(max-width:1600px) {
				a {
					p {
						font-size: r(18px);
						line-height: r(26px); } } }
			@media screen and(max-width:1200px) {
				a {
					p {
						font-size: r(16px);
						line-height: r(24px); } } }
			@media screen and(max-width:768px) {
				a {
					p {
						font-size: r(14px);
						line-height: r(21px); } } } }
		@media screen and(max-width: 576px) {
			flex-flow: column;
			.social-button {
				margin-bottom: 1rem; } } }
	.social-opinion {
		margin-top: 2rem;
		h2 {
			font-size: r(24px);
			font-family: "Muli";
			color: rgb(51, 51, 51);
			font-weight: bold;
			text-transform: uppercase;
			line-height: r(32px); }
		@media screen and(max-width:1600px) {
			h2 {
				font-size: r(22px); } }
		@media screen and(max-width:1200px) {
			h2 {
				font-size: r(20px); } }
		@media screen and(max-width:768px) {
			h2 {
				font-size: r(18px); }
			textarea {
				-webkit-box-sizing: border-box;
				-moz-box-sizing: border-box;
				box-sizing: border-box;
				width: 100%; } } }
	.social-comment {
		padding-bottom: 50px;
		@media screen and(max-width:1200px) {
			padding-bottom: 30px; }
		@media screen and(max-width:768px) {
			padding-bottom: 20px; } } }
