.pagination-verification {
	margin-bottom: 50px;

	ul {
		margin: 0;
		padding: 0;
		list-style-type: none;
		display: flex;
		align-items: center;
		li {
			display: inline;
			&:not(:last-child) {
				margin-right: .5rem; }
			&.active {
				a {
					color: #de1f25;
					border-color: #de1f25; } }
			a {
				text-decoration: none;
				display: inline-flex;
				padding: .25rem 1rem;
				border-radius: 50%;
				justify-content: center;
				align-items: center;
				border: 1px solid #999999;
				color: #999999;
				font-size: 24px;
				font-family: "Muli";
				font-weight: bold;
				object-fit: scale-down;
				em {
					display: flex;
					width: 1rem;
					justify-content: center;
					align-items: center; } } } }
	@media screen and(max-width:1600px) {
		ul {
			li {
				a {
					font-size: 18px;
					width: 1.5rem;
					height: 1.7rem; } } } }

	@media screen and(max-width:1200px) {
		margin-bottom: 30px;
		ul {
			li {
				padding: .25rem .5rem;
				&:not(:last-child) {
					margin-right: 0; }
				a {
					font-size: r(18px);
					em {
						line-height: 1.5rem;
						width: .5rem; } } } } }
	@media screen and(max-width:768px) {
		margin-bottom: 20px;
		ul {
			li {
				a {
					font-size: r(16px); } } } } }
