.media-iframe {
	.media-wrapper {
		position: relative;
		display: block;
		width: 100%;
		padding: 0;
		overflow: hidden;
		padding-top: 47.257143%;
		&:before {
			display: block;
			content: ""; }
		iframe {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border: 0; } }
	.media-title {
		margin: 1rem 0 3.5rem 0;
		h2 {
			font-size: r(24px);
			font-family: "Muli";
			color: rgb(51, 51, 51);
			font-weight: bold;
			line-height: r(32px); }

		span {
			font-size: r(17px);
			line-height: r(25px);
			font-family: "Muli";
			color: rgb(102, 102, 102);
			a {
				color: #0096ff;
				font-size: r(17px);
				text-decoration: underline;
				line-height: r(25px); } }
		@media screen and(max-width:1600px) {
			h2 {
				font-size: r(20px);
				line-height: r(26px); } }
		@media screen and(max-width:1200px) {
			margin: 1rem 0 2rem 0;
			h2 {
				font-size: r(18px);
				line-height: r(26px); }
			span {
				font-size: r(16px);
				line-height: r(23px);
				a {
					font-size: r(16px);
					line-height: r(23px); } } }
		@media screen and(max-width:768px) {
			margin: 1rem 0 1.5rem 0; } }
	.other-media-1 {
		margin-bottom: 50px;
		h2 {
			font-size: r(30px);
			line-height: r(38px);
			font-family: "Muli";
			color: rgb(222, 31, 37);
			text-transform: uppercase;
			margin-bottom: 1rem;
			font-weight: 900; }
		.media-fancyapp-wrapper {
			display: flex;
			justify-content: space-between;
			align-items: center;
			.media-fancy {
				a {
					text-decoration: none; }
				.fancy-image {
					position: relative;
					width: 90%;
					&:before {
						content: '';
						position: absolute;
						display: inline-block;
						background-image: url('../images/options-1/yt-icon.png');
						background-size: r(62px) r(42px);
						width: r(62px);
						background-position: center center;
						z-index: 5;
						background-repeat: no-repeat;
						height: r(42px);
						left: 50%;
						top: 50%;
						transform: translate(-50%,-50%); }
					&:after {
						content: '';
						display: block;
						position: absolute;
						left: 0;
						bottom: 0;
						width: 100%;
						height: 100%;
						background: rgba(#000,.3); } }
				img {
					max-width: 100%;
					height: auto;
					width: 100%; }
				h5 {
					margin-top: 1rem;
					font-size: r(20px);
					font-family: "Muli";
					color: rgb(51, 51, 51);
					font-weight: bold; } } }
		@media screen and(max-width:1600px) {
			h2 {
				font-size: r(26px);
				line-height: r(34px); }
			.media-fancyapp-wrapper {} }
		@media screen and(max-width:1200px) {
			margin-bottom: 30px;
			h2 {
				font-size: r(24px);
				line-height: r(30px); }
			.media-fancyapp-wrapper {
				.media-fancy {
					h5 {
						font-size: r(18px);
						line-height: r(25px); } } } }
		@media screen and(max-width:768px) {
			margin-bottom: 20px;
			h2 {
				font-size: r(20px);
				line-height: r(27px); }
			.media-fancyapp-wrapper {
				.media-fancy {
					h5 {
						font-size: r(14px);
						line-height: r(23px); } } } }
		@media screen and(max-width:576px) {
			margin-bottom: 0;
			.media-fancyapp-wrapper {
				flex-flow: column;
				.media-fancy {
					margin-bottom: 1rem;
					h5 {
						margin-top: .5rem;
						font-size: r(16px);
						line-height: r(23px); }
					.fancy-image {
						width: 100%; } } } } }
	@media screen and(max-width:1200px) {
		margin-bottom: 30px; }

	@media screen and(max-width:768px) {
		margin-bottom: 20px; } }



