.footer-2v2-payment {
	background: #ffffff;
	.footer-mobile-contact-options {
		font-family: 'Muli';
		font-size: r(16px);
		letter-spacing: r(16px*.0625);
		font-weight: 700;
		text-decoration: none;
		z-index: 99;
		margin-bottom: 2rem;
		list-style-type: none;
		padding: 0.4em 0;
		background: #fff;
		line-height: r(24px);
		color: #ed1c24;
		text-align: left;
		border: 1px solid #ed1c24;
		cursor: pointer;
		width: 100%;
		display: none;
		justify-content: center;
		flex-direction: row-reverse;
		position: relative;
		&:before {
			font-family: 'Material Design Icons';
			content: '\f35d';
			font-size: 1rem;
			position: absolute;
			right: 1rem;
			color: #ed1c24; }
		&.open {
			&:before {
				content: '\f360'; } }
		.default-footer-select {
			width: 100%;
			margin: 0 0;
			padding: 0 0;
			text-indent: 1rem;
			position: relative;
			list-style-type: none;
			.default-select-options {}

			.options {
				display: block;
				list-style-type: none;
				padding: 0;
				margin: 0 0;
				background: transparent;
				position: absolute;
				width: 100%;
				max-height: 0px;
				overflow: hidden;
				border: 3px solid transparent;
				top: 2rem;
				left: 0;
				transition: all ease 0.25s;
				li {
					padding: 0.4rem 0;
					border-bottom: 1px solid rgba(#2F2F3F,0.4);
					&:hover {
						background:rgba(#2F2F3F,0.2) {} }
					&:last-of-type {
						border-bottom: 0; } }
				&.open {
					background: #fff;
					max-height: 1000%;
					width: 100%;
					border: 1px solid #ed1c24;
					border-top: 3px solid transparent; } } } }
	.footer-contact-wrapper {
		background: #ffffff;
		padding: r(45px) 0;
		.payment-partner-wrapper {
			h4 {
				font-size: r(16px);
				letter-spacing: r(16px*.0625);
				line-height: r(24px);
				font-family: 'Muli';
				color: rgb(51, 51, 51);
				font-weight: bold;
				text-transform: uppercase; }
			.payment-partner-logo {
				a {
					display: inline-block;
					margin: .2rem;
					img {} } } }
		.instalment-payment {
			h4 {
				font-size: r(16px);
				letter-spacing: r(16px*.0625);
				line-height: r(24px);
				font-family: 'Muli';
				color: rgb(51, 51, 51);
				font-weight: bold;
				text-transform: uppercase; }
			img {
				margin: 1.2rem 0 1rem 0; }
			p {
				font-size: r(14px);
				font-family: 'Muli';
				color: rgb(102, 102, 102);
				letter-spacing: r(14px*.0135);
				line-height: r(22px);
				margin-bottom: 0.5rem; } }
		.support {
			h4 {
				font-size: r(16px);
				font-family: 'Muli';
				color: #333333;
				font-weight: bold;
				text-transform: uppercase;
				letter-spacing: r(16px*.0625);
				line-height: r(24px); }
			h5 {
				font-size: r(14px);
				margin: 1.2rem 0 .5rem 0;
				font-family: 'Muli';
				color: #666666;
				font-weight: bold;
				letter-spacing: r(16px*.0625);
				line-height: r(24px); }
			a {
				display: flex;
				justify-content: flex-start;
				align-items: center;
				text-decoration: none;
				font-weight: bold;
				color: #0084cb;
				font-size: r(16px);
				margin: .5rem 0;
				font-family: 'Muli';
				letter-spacing: r(16px*.0625);
				line-height: r(24px);
				text-decoration: underline; }
			p {
				font-size: r(16px);
				font-family: 'Muli';
				color: #666666;
				font-weight: 400;
				letter-spacing: r(16px*.0625);
				line-height: r(24px);
				text-decoration: underline; }
			.phone {
				span {
					display: flex;
					justify-content: flex-start;
					align-items: center;
					a {
						margin: 0 0 .5rem 0;
						text-decoration: none;
						img {}
						p {
							margin-left: .5rem;
							text-decoration: none;
							margin-bottom: 0;
							font-size: r(20px);
							font-family: 'Muli';
							color: #ed1c24;
							font-weight: bold;
							text-transform: uppercase;
							letter-spacing: r(16px*.0625);
							line-height: r(24px); } } } } }
		.verification {
			h4 {
				font-size: r(16px);
				letter-spacing: r(16px*.0625);
				line-height: r(24px);
				font-family: 'Muli';
				color: rgb(51, 51, 51);
				font-weight: bold;
				text-transform: uppercase; }
			img {
				padding: r(10px) 0 r(15px) 0; }
			h5 {
				font-size: r(14px);
				font-family: 'Muli';
				color: rgb(102, 102, 102);
				font-weight: bold;
				text-transform: uppercase;
				letter-spacing: r(14px*.0625);
				line-height: r(22px); }

			p {
				margin-bottom: 0;
				font-size: r(14px);
				font-family: 'Muli';
				color: rgb(102, 102, 102);
				font-weight: 400;
				letter-spacing: r(14px*.0625);
				line-height: r(22px); } } }
	@media screen and(max-width:992px) {
		.footer-contact-wrapper {
			.verification {
				margin-top: 1rem; }
			.support {
				margin-top: 1rem; } } }
	@media screen and(max-width:768px) {
		.footer-mobile-contact-options {
			display: flex;
			margin: 2rem 0 0 0; }
		.footer-contact-wrapper {
			padding: 1rem 0 r(45px) 0;
			.mb-option {
				display: none;
				&.active-selected {
					display: block; } }
			.payment-partner-wrapper {
				margin-bottom: 0; }
			.instalment-payment {
				margin-bottom: 0;
				h4 {
					margin: 0 0; } }
			.verification {
				margin-bottom: 0; }
			.support {
				margin: 0 0; } } }
	@media screen and(max-width:576px) {
		.footer-mobile-contact-options {}
		.footer-contact-wrapper {} } }

.footer-2-design {
	background: #ed1c24;
	padding: 1rem 0;
	.design-wrapper {
		display: flex;
		justify-content: center;
		align-items: center;

		.copy-right {
			padding: 0 0;
			text-align: center;
			p {
				white-space: nowrap;
				margin-bottom: 0;
				font-size: r(16px);
				font-family: 'Muli';
				color: #ede2cd;
				letter-spacing: r(16px*.0625);
				line-height: r(24px); } }
		@media screen and(max-width: 768px) {
			.copy-right {
				p {
					font-size: r(14px); } } } } }
