.article-body {
	padding: 0 8.33333333333%;
	margin-bottom: r(50px);
	artcile {
		img {
			max-width: 100%;
			height: auto;
			width: 100%;
			margin-bottom: 1rem; }
		h4 {
			margin: 1.5rem 0;
			font-family: 'Muli';
			font-size: r(24px);
			font-weight: 400;
			line-height: r(30px);
			text-transform: uppercase;
			font-weight: 700;
			color: #333333; }
		h5 {
			font-family: 'Muli';
			font-size: r(20px);
			font-weight: 700;
			line-height: r(27px);
			text-transform: uppercase;
			color: #de1f25; }
		p {
			font-family: 'Muli';
			font-size: r(20px);
			font-weight: 400;
			line-height: r(28px);
			color: #666666;
			strong {
				color: #333333; } }
		.docs-list-red,.docs-list-grey {
			list-style-type: none;
			counter-reset: section;
			margin: 0;
			padding: 0;
			li {
				counter-increment: section;
				font-family: 'Muli';
				font-size: r(20px);
				line-height: r(28px);
				color: #333333;
				font-weight: 700;
				&:before {
					content: counters(section, ".");
					margin-right: .5rem;
					font-size: r(18px);
					width: 2rem;
					height: 2rem;
					color: #fff;
					display: inline-flex;
					justify-content: center;
					align-items: center;
					border-radius: 50%; }

				p {
					padding-top: .5rem; } } }
		.docs-list-red {
			li {
				&:before {
					background: #ed1c24; } } }
		.docs-list-grey {
			li {
				&:before {
					background: #666666; } } }
		.docs-list-dots {
			list-style-type: none;
			margin: 0;
			padding: 0;
			li {
				font-family: 'Muli';
				font-size: r(20px);
				font-weight: 400;
				line-height: r(28px);
				margin-top: .5rem;
				color: #666666;
				padding-left: 1rem;
				&:before {
					content: '\2022';
					color: #ed1c24;
					font-size: x-large;
					display: inline-block;
					width: 1rem;
					margin-left: -1rem; }
				ol {
					padding: 0  0 0 r(20px);
					display: block;
					list-style-type: none;
					li {
						&:before {
							content: '\25A0';
							font-size: medium; } } } } }
		.list-normal {
			list-style-type: none;
			li {
				p {
					margin: 0 0 .5rem 0; }
				&:before {
					content: none; } } } }
	@media screen and(max-width:1600px) {
		artcile {

			h4 {
				font-size: r(22px);
				line-height: r(26px); }

			h5 {
				font-size: r(16px);
				line-height: r(24px); }

			p {
				font-size: r(16px);
				line-height: r(24px); }
			.docs-list-red,.docs-list-grey {
				li {
					font-size: r(16px);
					line-height: r(24px);
					&:before {
						font-size: r(16px);
						width: 1.5rem;
						height: 1.5rem;
						color: #fff; }

					p {
						padding-top: .5rem; } } }
			.docs-list-red {
				li {} }
			.docs-list-grey {
				li {} }
			.docs-list-dots {
				li {
					font-size: r(16px);
					line-height: r(24px); } }
			.list-normal {
				li {
					p {} } } } }
	@media screen and(max-width:1200px) {
		padding: 0 0;
		margin-bottom: r(30px); } }
