.product-original-v3, {
	padding: 1.5rem 0;
	.product-title-wrapper {
		.product-title {
			text-align: center;
			h2 {

				font-size: r(36px);
				color: #EC1F26;
				font-family: 'Muli';
				line-height: r(44px);
				font-weight: 900; } }
		.product-tab-nav {
			display: flex;
			justify-content: center;
			align-items: center;
			ul {
				display: flex;
				list-style-type: none;
				overflow: hidden;
				width: 100%;
				justify-content: center;
				padding: 0.8rem 0 r(8px) 0;
				margin-bottom: 0;
				li {
					&:not(:last-child) {
						margin-right: 1rem; }
					&.active {
						a {
							color: #533d0d;
							background: url('../images/options-1/bg-linear-gradient-gold-small.png');
							background-size: cover;
							background-position: center;
							background-repeat: no-repeat;
							-webkit-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							-moz-box-shadow: 3px 3px 0px 0px rgba(111,50,5,1);
							box-shadow: 3px 3px 0px 0px rgba(111,50,5,1); } }
					a {
						display: flex;
						justify-content: center;
						align-items: center;
						padding: .65rem 2rem;
						text-decoration: none;
						color: #fff;
						background: #EC1D25;
						font-family: 'Muli';
						font-size: r(18px);
						line-height: r(24px);
						font-weight: 900; } } } }
		.view-more {
			border-bottom: 1px solid #EC1F26;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 0 r(10px) r(10px) r(10px);
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				color: #EC1F26;
				font-size: r(18px);
				font-family: 'Muli';
				line-height: r(24px);
				font-weight: 700; } }
		@media screen and(max-width:1600px) {
			.product-title {
				h2 {
					font-size: r(28px);
					line-height: r(34px); } }
			.product-tab-nav {
				ul {
					li {
						a {
							font-size: r(15px);
							padding: .5rem 1rem; } } } } }
		@media screen and(max-width:1200px) {
			.product-tab-nav {
				ul {
					li {
						a {
							font-size: r(14px);
							line-height: r(22px);
							padding: .5rem .5rem; } } } } }
		@media screen and(max-width:992px) {
			.product-title {
				h2 {
					font-size: r(24px);
					line-height: r(32px); } }
			.product-tab-nav {
				ul {
					li {
						&:not(:last-child) {
							margin-right: .5rem; }
						a {
							font-size: r(13px);
							line-height: r(21px);
							padding: .5rem .35rem;
							font-weight: 700;
							white-space: nowrap; } } } } }
		@media screen and(max-width:768px) {
			.product-tab-nav {
				ul {
					li {
						&:nth-child(n+3) {
							display: none; }
						a {
							font-size: r(16px);
							line-height: r(24px);
							padding: .5rem .75rem; } } } } }
		@media screen and(max-width:576px) {
			.product-tab-nav {
				ul {
					li {
						&:not(:last-child) {
							margin-right: .45rem; }
