.kingsport-warranty-check {
	padding: r(20px) 0;
	margin-bottom: 1.25rem;
	.kingsport-warranty-wrapper {
		background: url('../images/options-1/bg-linear-gradient-gold-medium.png');
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		padding: r(20px) 0;
		h2 {
			font-size: r(18px);
			line-height: r(26px);
			font-family: "Muli";
			color: rgb(83, 61, 13);
			font-weight: 400;
			text-align: center;
			margin-bottom: .75rem; }
		.kingsport-warranty-form {
			.form-group {
				display: flex;
				justify-content: center;
				align-items: center;
				input {
					background: rgba(#fff,.5);
					height: r(40px);
					padding: .2rem 0 .2rem 1.5rem;
					width: r(500px);
					border: none;
					&:focus {
						outline: none; }
					&::placeholder {
						font-size: r(16px);
						font-family: "Muli";
						color: rgb(102, 102, 102);
						line-height: r(24px); } }
				.form-button {
					margin-left: .5rem;
					a {
						position: relative;
						overflow: hidden;
						padding: .4rem 2rem;
						font-size: r(20px);
						line-height: r(28px);
						text-decoration: none;
						background: #cc0000;
						color: #ffffff;
						display: flex;
						justify-content: center;
						align-items: center; } } } } }
	@media screen and(max-width:1600px) {
		.kingsport-warranty-wrapper {
			h2 {
				font-size: r(17px);
				line-height: r(23px);
				padding: 0 1rem; }
			.kingsport-warranty-form {
				.form-group {
					input {
						width: r(400px);
						&:placeholder {
							font-size: r(15px);
							line-height: r(23px); } }
					.form-button {
						a {
							font-size: r(18px);
							line-height: r(26px); } } } } } }
	@media screen and(max-width:768px) {
		.kingsport-warranty-wrapper {
			background: url('../images/options-1/bg-form.png');
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			h2 {
				font-size: r(15px);
				line-height: r(23px); }
			.kingsport-warranty-form {
				.form-group {
					input {
						width: r(300px);

						&::placeholder {
							font-size: r(14px);
							line-height: r(22px); } }
					.form-button {
						margin: 0 0;
						a {
							padding: .8rem 1rem;
							font-size: r(14px);
							line-height: r(14px); } } } } } }
	@media screen and(max-width:500px) {
		.kingsport-warranty-wrapper {
			h2 {
				font-size: r(14px);
				line-height: r(22px);
				padding: 0 1rem; }
			.kingsport-warranty-form {
				.form-group {
					input {
						width: r(250px);
						&::placeholder {
							font-size: r(14px);
							line-height: r(22px); } } } } } }
	@media screen and(max-width:400px) {
		.kingsport-warranty-wrapper {

			.kingsport-warranty-form {
				.form-group {
					input {
						width: r(200px); }
					.form-button {
						a {
							padding: .8rem .75rem; } } } } } } }
