	.product-rating-wrapper {
		display: inline;
		.product-rating {
			display: inline; }
		.product-rating-display {
			color: transparent;
			display: inline-block;
			overflow: hidden;
			margin-right: 5px;
			position: relative;
			vertical-align: middle;
			&:before,&:after {
				font-family: 'Material Design Icons';
				content: "\f4ce \f4ce \f4ce \f4ce \f4ce";
				font-size: 1.2rem; }
			&:before {
				display: inline-block;
				background: #cccccc;
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent; }
			&:after {
				display: inline-block;
				background: -moz-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: -webkit-gradient(linear, left bottom, right top, color-stop(0%, rgba(255,0,0,1)), color-stop(50%, rgba(255,255,0,1)), color-stop(100%, rgba(255,0,0,1)));
				background: -webkit-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: -o-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: -ms-linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				background: linear-gradient(45deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 50%, rgba(255,0,0,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff0000', endColorstr='#FF0000',GradientType=1 );
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				overflow: hidden;
				position: absolute;
				left: 0;
				top: 0;
				width: 0; } }
		.product-rating-display[data-rating="5.0"]:after {
			width: 100%; }
		@for $wholeRating from 0 through 4 {
			@for $decimalRating from 0 through 9 {
				.product-rating-display[data-rating="#{$wholeRating}.#{$decimalRating}"]:after {
					@if $wholeRating == 0 and $decimalRating <= 4 {
						width: 0; }
					@else {
						width: percentage( ((($wholeRating * 10) + $decimalRating ) * .1) /5); } } } }
		.rating {
			padding-left: 1rem;
			font-size: r(20px);
			font-family: "Muli";
			color: rgb(237, 28, 36); } }
