.social-network-media-player-2 {
	margin-top: 1rem;
	.media-title-2 {
		display: flex;
		justify-content: center;
		align-items: center;
		h2 {
			text-align: center;
			margin-bottom: 1rem;
			font-size: r(36px);
			color: #EC1F26;
			font-family: 'Muli';
			line-height: r(44px);
			font-weight: 900; } }
	.social-media-player-wrapper {
		background: #f7f7f7;
		padding: r(50px) r(20px) r(30px) r(50px);
		.line {
			&:first-child {
				border-right: 30px solid rgba(#000,0); } }
		.social-media-content {
			position: relative;
			display: block;
			width: 100%;
			padding: 0;
			overflow: hidden;
			padding-top: 58.257143%;
			&:before {
				display: block;
				content: ""; }
			iframe {
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border: 0; } } }
	.social-media-caption {
		padding-left: 1rem;
		p {
			margin-bottom: .5rem;
			font-size: r(20px);
			font-family: "Muli";
			color: rgb(102, 102, 102);
			line-height: r(25px); } }
	@media screen and(max-width:1600px) {
		.media-title-2 {
			h2 {
				font-size: r(28px);
				line-height: r(34px); } }
		.social-media-caption {
			p {
				font-size: r(18px); } } }
	@media screen and(max-width:1200px) {
		.social-media-player-wrapper {
			padding: 30px 0 30px 0; }
		.social-media-caption {
			margin-top: 1rem; } }
	@media screen and(max-width:992px) {
		.media-title-2 {
			h2 {
				font-size: r(24px);
				line-height: r(32px); } }
		.social-media-caption {
			p {
				font-size: r(16px); } } }
	@media screen and(max-width:768px) {
		.media-title-2 {
			h2 {
				font-size: r(20px);
				line-height: r(27px); } } } }
